// 🏠️ OSP-4117

@import '../../modules';

.main-osp4117{


  .map__wrapper{
    padding: 9.6rem 0;

    address{
      
      .addr-map{
        @include style_font(500, 2.4rem, 1, #212121);

        position: relative;
        margin-bottom: 4rem;
        padding-left: 4rem;

        // 파란 점
        &::before{
          position: absolute;
          content: '';
          top: 0rem;
          left: 0rem;
          width: 2rem;
          height: 2rem;
          background-image: url('../../../../public/asset/icon/icon_subtract.svg');
          background-size: cover;
        }
      }
      
      p{
        padding-right: 4rem;
        margin-bottom: 2.2rem;
        @include style_font(400, 2rem, 1, #666666);
        span{
          width: 7.6rem;
          font-weight: 700;
        }
      }
    }

    iframe{
      margin-top: 1.8rem;
      width: 100%;
      height: 67.1rem;
    }
  }
}




// * 👽️responsive
@include mobile-max{
  .main-osp4117{
    .tab__bar-style{
      ul li {
        width: 50%;
      }
    }

    .map__wrapper{
      padding: 4.8rem 0;

      address{
        padding: 0 2rem;
        
        .addr-map{
          margin-bottom: 2.8rem;
          padding-left: 2.7rem;
          
          font-weight: 700;
          font-size: 1.4rem;

          &::before{
            width: 1.5rem;
            height: 1.5rem;
          }
        }

        p{
          margin-bottom: 1.6rem;
          padding-left: 2.7rem;

          font-size: 1.3rem;

          span{
            width: auto;
            padding-right: 2.3rem;
          }
        }
      }

      iframe{
        margin-top: 3.2rem;
        height: 23.1rem;

      }

    }

  }
}