@import '../../modules';

.main-osp0113{
  padding-top: 9.6rem;
  padding-bottom: 14.8rem;
  text-align: center;
  background: $bg_gray_f;
  
  h3{
    @include style_font(700, 3.6rem, 1, #1f1f1f);
    padding-bottom: 4.4rem;
  }

  p{
    @include style_font(400, 1.8rem, 1, #666666);
    
    .icon{
      width: 2.4rem;
      height: 2.4rem;
      background-image: none;
      margin-right: 1.2rem;
      vertical-align: middle;
    }
  }

  .img-github{
    display: inline-block;
    width: 48.9rem;
    height: 14rem;
    margin: 12.8rem auto;
    background-image: url($img_path_to_pages + 'img-OSP0112-git.png');
    background-size: cover;
  }

  .btn{
    // display: block;
    width: fit-content;
    margin: 0 auto;
    font-weight: 700;
    padding: 1.6rem 4.5rem;
    font-size: 2.2rem;

    .icon{
      vertical-align: -0.4em;
    }
  }
}

@include mobile-max{
  .main-osp0113{
    padding-top: 4rem;
    padding-bottom: 4rem;

    h3{
      width: 70%;
      margin: 0 auto;
      word-break: keep-all;
      font-size: 1.6rem;
      padding-bottom: 3rem;

      line-height: 1.5em;
    }

    p{
      position: relative;
      width: 80%;
      padding-left: 2rem;
      margin: 0 auto;
      font-size: 1.3rem;
      text-align: left;
      line-height: 1.5em;
      word-break: keep-all;

      .icon{
        position: absolute;
        left: 0;

        width: 1.5rem;
        height: 1.5rem;
      }
    }

    .img-github{
      margin: 8rem auto;
      width: 21rem;
      height: 6rem;
    }

    .btn{
      padding: 0;
      width: 31.3rem; 
      height: 3.6rem;

      font-size: 1.4rem;
    }
  }
}