@import '../../modules';



.main-osp4116{

  strong{
    color: $azure;
  }

  h1{
    @include style_font(700, 3.6rem, 1, #1F1F1F);
    padding-top: 6.4rem;
    padding-bottom: 14rem;
    text-align: center;

    strong{
      display: block;
      padding-top: 2rem;
    }
  }

  // 연혁 내용
  .history__container{
    position: relative;
    padding-bottom: 23.7rem;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
    // 모든 연혁 컨텐츠
    .history{
      position: relative;
      padding-top: 24.4rem;
      margin-bottom: 3.2rem;

      // 화살표
      &::before{
        position: absolute;
        content: '';
        display: inline-block;

        right: 12.8rem;
        bottom: 28rem;
        // bottom: %;
        // top: -88%;

        width: 2.3rem;
        height: 28rem;

        background-image: url($img_path_to_pages + 'history/history_arr.png');
        background-position: bottom;
        background-size: auto 24rem;
        background-repeat: no-repeat;
      }

      // 이미지 배경 영역 
      .img{
        position: relative;
        display: block;
        width: 28rem;
        height: 28rem;
        background-color: $bg_gray_f;
        border-radius: 50%;
        

        // 이미지 영역 - after에 넣음
        &::after{
          position: absolute;
          content: '';
          top: -3.2rem;
          
          width: 24rem;
          height: 24rem;
          background-repeat: no-repeat;
          background-size: cover;

          opacity: 0;
          transform: translateY(5rem);
          transition: all 0.3s;
          transition-delay: 0.1s;
        }
      }
      .img_history-1::after{
        background-image: url($img_path_to_pages + 'history/history_2023.png');
      }
      .img_history-2::after{
        background-image: url($img_path_to_pages + 'history/history_2021.png');
      }
      .img_history-3::after{
        background-image: url($img_path_to_pages + 'history/history_2018.png');
      }
      .img_history-4::after{
        background-image: url($img_path_to_pages + 'history/history_2010.png');
      }

      // 내용
      .history-content{
        position: absolute;
        width: max-content;
        bottom: -3rem;

        h3{
          @include style_font(700, 3.4rem, 1, $font_color-212121_grey);
          padding-bottom: 5.2rem;
        }

        ul{
          li{
            padding-bottom: 4.4rem;
            p{
              @include style_font(400, 2.4rem, 1, #666);
              strong{
                @include style_font(700, 3.2rem, 1, $azure);
              }
            }
          }
          li:last-child{
            padding-bottom: 0;
          }
        }
      }
    }

    // 첫 번째 연혁 
    .history.h--01{
      padding-top: 2rem;
      
      // 화살표가 없음
      &::before{
        display: none;
      }
    }

    // 방향에따라 바뀜
    .history.--right{
      .img::after{
        left: -5.8rem;
      }

      .history-content{
        left: 23.7rem;
        strong{
          padding-right: 1.6rem;
        }
      }
    }

    .history.--left{
      .img::after{
        right: -5.8rem;
      }

      .history-content{
        text-align: right;
        right: 23.7rem;
        strong{
          padding-left: 1.6rem;
        }
      }
    }
  }
}


// * 🦄️Interaction
.main-osp4116.fixed{
  margin-top: 25.2rem;

  .history.--active{
    .img::after{
      opacity: 1;
      transform: translateY(0);
    }
  } 
}

// * 👽️responsive
@include mobile-max{

  .main-osp4116.fixed{
    margin-top: 13.1rem;
  }

  .main-osp4116{

    h1{
      font-size: 1.6rem;
      padding-top: 0;
    }

    .history__container{
      padding-bottom: 12.7rem;

      .history{
        padding-top: 20.2rem;

        // 화살표
        &::before{
          width: 9.5px;
          height: 12rem;
          background-size: auto 12rem;

            right: calc(50% + 9.5px);
          bottom: 45%;
        }

        // 이미지
        .img{
          width: 15rem;
          height: 15rem;

          &::after{
            width: 10rem;
            height: 10rem;
          }
        }

        .history-content{
          // width: max-content;

          h3{
            font-size: 1.5rem;
            padding-bottom: 2.3rem;
          }

          ul{
            li{
              padding-bottom: 1.9rem;
              p{
                font-size: 1.4rem;
                strong{
                  display: block;
                  font-size: 1.5rem;
                  padding-bottom: 1rem;
                }
              }
            }
          }
        }
      }
      
      // 첫 번째 연혁 - 화살표가 없음
      .history.h--01{
        padding-top: 8rem;

      }

      .history.--right{



        .img{
          margin-right: 5.25rem;
        }
        .img::after{
          left: -3.1rem;
          top: -1.5rem;
        }

        .history-content{
          left: 8.8rem;

          strong{
            padding-right: 1.6rem;
          }
        }
      }
      
      .history.--left{

        .img{
          margin-left: 5.25rem;
        }
        .img::after{
          right: -3.1rem;
          top: -1.5rem;
        }

        .history-content{
          right: 8.8rem;

          strong{
            padding-left: 1.6rem;
          }

        }
      }
    }
  }
}