// 🏠️ OSP-5114

@import '../../modules';


.main-osp5114{
  text-align: center;
  padding-bottom: 12rem;
  
  // superrds 특징 - 메인
  .superrds-feature--main{
    position: relative;
    padding-top: 8.4rem;
    
    h1{
      @include style_font(900, 4rem, 1, $font_color-212121_grey);
      letter-spacing: 0;
      padding-bottom: 16.5rem;

      strong{
        color: $azure;
      }
    }

    .illust-superrds-main{
      width: 1301px;
      height: 412px;

      background-image: url($img_path_to_pages + 'product/temp.png');
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
    }

    // 메인 특징들
    .main-feature{
      margin-top: 17.6rem;

      .feature-list{
        display: flex;
        justify-content: center;
        gap: 4rem;

        li{
          width: 57.4rem;
          border-radius: 2rem;
          overflow: hidden;
          box-shadow: 0 40px 40px rgba(0, 0, 0, 0.04);
          background-color: white;

          h4{
            @include style_font(700, 2.4rem, 1, white);
            padding: 3.2rem 0;
            background-color: $azure_emp_02;
          }

          .feature-depth2{
            height: 34.9rem;
            padding-top: 3.7rem;
            padding-bottom: 7rem;

            .img{
              width: 100%;
              height: 100%;
              background-position: center center;
            }
            
            .img-superrds-feature-vdi{
              background-image: url($img_path_to_pages + 'product/img-superrds-feature-vdi.svg');
              background-size: 30.5rem 9.4rem;
            }
            
            .img-superrds-feature-tr{
              background-image: url($img_path_to_pages + 'product/img-superrds-feature-tr.svg');
              background-size: 32rem 15rem;
            }

            p{
              @include style_font(400, 2rem, 1, $font_color-212121_grey);
            }
          }
        }
      }
    }

    // bg
    .bg{
      display: block;

      width: 100%;
      height: 569px;
      padding-top: 35.3rem;
      margin-top: -34.9rem;

      background-color: $bg_gray_f;
    }
  }

  // superrds 특징 - (특장점)
  .superrds-feature--business{
    background: linear-gradient(270deg, #44BCFF 0%, #4BA5FF 100%);

    // 특장점 - 리스트
    .business-list{
      display: flex;
      justify-content: center;
      gap: 2.3rem;

      padding: 11.6rem 0;
      flex-wrap: wrap;


      li{
        // flex: 33.33%;
        width: 46.4rem;
        height: 61.2rem;
        padding-top: 5.4rem;

        border-radius: 3rem;
        background-color: white;

        .icon{
          width: 36.4rem;
          height: 21.8rem;

          // 아이콘 들
          &.icon-superrds-business-sec{
            background-image: url($img_path_to_pages + 'product/icon-superrds-business-sec.svg');
          }
          &.icon-superrds-business-fa{
            background-image: url($img_path_to_pages + 'product/icon-superrds-business-fa.svg');
          }        
          &.icon-superrds-business-st{
            background-image: url($img_path_to_pages + 'product/icon-superrds-business-st.svg');
          }
        }

        h6{
          @include style_font(700, 3.2rem, 1, $azure_emp_01);
          padding-top: 8.5rem;
          padding-bottom: 4rem;
        }

        p{
          position: relative;
          text-align: left;
          margin-left: 7rem;
          
          @include style_font(400, 1.8rem, 2em, #212121);
          
          // list style
          &::after{
            position: absolute;
            content: '';
            left: -1.8rem;
            top: 1.4rem;
            width: 6px;
            height: 6px;
            border-radius: 50%;
            background-color: $azure_emp_01;
          }
        }
      }
    }

    // 특장점 - 설명
    .business-explain{
      padding-top: 17rem;
      background-color: white;
      
      // 타이틀
      h2{
        @include style_font(700, 3.6rem, 1, $black_0);
        padding-bottom: 22.7rem;
        
        .basic{
          display: block;
          padding-bottom: 2rem;
        }
      }
      
      // 일러스트
      .svg{
        width: 1083px;
        height: 581px;

        &.svg-illust-expl{
          background-size: 100% 85%;
          background-repeat: no-repeat;
          background-image: url($img_path_to_pages + 'product/svg-illust-expl.svg');
        }
      }
    }
  }

  // superrds 특징 - 예시
  .superrds-feature--expl{

    // 예시 리스트
    .expl-list{
      margin-top: 25.4rem;
      display: flex;
      justify-content: center;
      gap: 4rem;
      // height: 58.5rem;
      flex-wrap: wrap;

      li{
        border-radius: 2rem;
        overflow: hidden;
        box-shadow: 0 40px 40px rgba(0, 0, 0, 0.04);
        width: 49.7rem;
        background-color: white;

        h4{
          @include style_font(700, 2.4rem, 1, white);
          padding: 3.2rem 0;
          background-color: $azure;
        }

        .expl-depth2{
          padding-top: 3.7rem;
          padding-bottom: 2rem;
    
          // 이미지 공통
          .img{
            display: inline-block;
            width: 34.4rem; 
            height: 25.6rem;

            background-size: 100% 100%;
            background-repeat: no-repeat;
            background-position: center center;

            &.img-expl-so{
              background-image: url($img_path_to_pages + 'product/img-expl-so.svg');
            }
            &.img-expl-hw{
              background-image: url($img_path_to_pages + 'product/img-expl-hw.svg');
            }
            &.img-expl-out{
              background-image: url($img_path_to_pages + 'product/img-expl-out.svg');
            }
          }

          p{
            @include style_font(400, 2.4rem, 1.6em, $black_0);
            padding-top: 4.2rem;
          }
        }
      }
    }

    // bg
    .bg{
      display: block;
      margin-top: -49.7rem;
      padding-top: 49.7rem;
      background-color: $bg_gray_f;
      height: 717px;
      width: 100%;
    }
  }

  .notice-text{
    margin-top: 16rem;
    margin-bottom: 18.6rem;
  }
}


// * 🦄️Interaction
.main-osp5114.fixed{
  margin-top: 25.2rem;
}


// * 👽️responsive
@include mobile-max{

  .main-osp5114{
    padding-bottom: 0;

    .superrds-feature--main{
      padding-top: 7rem;

      h1{
        font-size: 1.8rem;
        line-height: 1.8em;
        padding-bottom: 4.8rem;

        strong{
          display: block;
        }
      }

      .illust-superrds-main{
        width: 100%;
        height: 20rem;
        background-image: url($img_path_to_pages + 'product/temp_m.svg');
      }

      .main-feature{
        margin-top: 8rem;

        .feature-list{
          flex-direction: column;
          padding: 0 3rem;

          li{
            width: 100%;
            border-radius: 1rem;

            h4{
              padding: 1.3rem 0;
              font-size: 1.4rem;
            }

            .feature-depth2{
              height: 20.6rem;
              padding-top: 0;
              padding-bottom: 4.2rem;
              
              .img-superrds-feature-vdi{
                background-size: 20rem auto;
              }
              
              .img-superrds-feature-tr{
                background-size: 22rem auto;

              }

              p{
                font-size: 1.2rem;
              }
            }
          }
        }
      }

      .bg{
        margin-top: -49.2rem;
      }
    }

    // superrds 특징 - (특장점)
    .superrds-feature--business{

      // 특장점 - 리스트
      .business-list{
        padding: 5rem 4rem;

        row-gap: 2rem;

        li{
          width: 100%;
          height: max-content;
          padding: 2rem 0 4rem 0;

          .icon{
            // width: 8rem;
            // height: 8rem;
            width: 100%;
            height: 15.5rem;
            background-size: contain;
            background-position: center;
            
          }

          h6{
            font-size: 2rem;
            padding: 2.8rem 0 2rem 0;
          }

          .bar{
            width: 1.4rem;
            height: 3px;
          }
          p{
            font-size: 1.4rem;
            // padding-top: 2rem;
            margin-left: 4rem;

            &::after{
              width: 3px;
              height: 3px;
            }
          }

        }
      }

      // 특장점 - 설명
      .business-explain{
        padding-top: 7rem;
        padding: 7rem 3rem 0 3rem;

        h2{
          font-size: 1.8rem;
          line-height: 1.8em;
          padding-bottom: 7rem;

          .basic{
            padding-bottom: 0;
          }

          .highlight{
            &::after{
              height: 1rem;
              bottom: 0.4rem;
            }
          }
        }

        .svg{
          width: 100%;  
          height: 16rem;
        }
      }

    }

    // superrds 특징 - 예시
    .superrds-feature--expl{
      .expl-list{
        margin-top: 9.5rem;
        padding: 0 4rem;

        li{
          border-radius: 1rem;

          h4{
            padding: 1.3rem 0;
            font-size: 1.4rem;
          }

          .expl-depth2{
            padding: 2.2rem 3rem 3.7rem 3rem;

            .img{
              width: 100%; 
              height: 16.4rem; 
            }

            p{
              font-size: 1.4rem;
              padding-top: 2rem;
            }
          }
        }
      }

      .bg{
        margin-top: -102rem;
        height: 108rem;
      }
    }
    .notice-text{
      margin-top: 8rem;
      margin-bottom: 9.7rem;
    }
  }


  // * 🦄️Interaction - mobile
  .main-osp5114.fixed{
    margin-top: 13.1rem;
  }
}