// 🏠️ OSP-4113

@import '../../modules';

.main-osp4113{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 14.8rem;
  padding-bottom: 18rem;

  // 타이틀 - 조직
  .tmaxos-group{
    margin-bottom: 18rem;
    display: flex;

    // 조직 개요
    .group-title{
      padding-top: 14.7rem;

      h3{
        @include style_font(900, 3.2rem, 1.2, $azure);

        strong{
          font-size: 72px;
        }
      }

      h2{
        @include style_font(700, 4rem, 1.8, black);

        padding-top: 1.6rem;
        padding-bottom: 2.8rem;
        padding-right: 23.8rem;

      }
      p{
        font-size: 2.4rem;
        line-height: 1.2;
      }
    }

    // 개요 이미지 
    .img_tmaxos_group-title{
      position: relative;
      display: block;

      min-width: 683px;
      height: 683px;

      border-radius: 50%;
      background-image: url($img_path_to_pages + 'img_OSP4113.png');
      background-size: cover;

      // 회색 원
      &::before{
        position: absolute;
        content: '';
        top: 0;
        left: -65%;

        width: 100%;
        height: 100%;

        background-color: $bg_gray_f;
        border-radius: 50%;
        z-index: -9;
      }
    }
  }

  // 조직도 svg
  .svg_tmaxos-group{
    width: 1190px;
    height: 948px;
  }
}





// * 👽️responsive
@include mobile-max{
  .main-osp4113{
    padding: 4.6rem 3.2rem 8rem 3.2rem;

    .tmaxos-group{
      width: 100%;
      margin-bottom: 8rem;

      // flex-wrap: wrap;
      flex-direction: column;
      justify-content: center;

      .group-title{
        padding-top: 4rem;
        padding-bottom: 2.8rem;
        h3{
          font-size: 1.4rem;
          strong{
            font-size: 3.2rem;
          }
        }

        h2{
          font-size: 1.6rem;
          padding: 0;
          padding-top: 6px;
          padding-bottom: 7px;
        }

        p{
          font-size: 1.2rem;
        }
      }

      .img_tmaxos_group-title{
        min-width: 27.1rem;
        height: 27.1rem;

        padding-bottom: 100%;


        // 회색 원
        &::before{
          left: 0;
          top: -19.1rem;
        }
      }
    }

    .svg_tmaxos-group{
      width: 100%;
      height: auto;
      
    }
  }
}


