@import '../../modules';

// 임시 작업
.temp-layout-grcommu{
  position: relative;

  &::before{
    position: absolute;
    content: '';
    display: block;
    width: 100%;
    height: 100%;

    background-color: rgba(0, 0, 0, 0.6);;
    z-index: 9;
  }
  
  .temp-message{
    position: absolute;
    display: flex;
    flex-direction: column;

    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;

    color: $white;
    font-size: 4.2rem;
    font-weight: 700;
    z-index: 99;

    .icon-temp-img{
      max-width: 65rem;
      max-height: 65rem;

      width: 70vw;
      height: 70vw;
      background-size: 100%;
      background-repeat: no-repeat;
    }
  }
}


.main-osp0111{

  .community-main{
    padding-top: 9.6rem;
    padding-bottom: 15.2rem;
    text-align: center;

    h1{
      @include style_font(500, 4rem, 1, $font_color-212121_grey);
      word-break: keep-all;
      margin: 0 auto;

      strong{
        font-weight: 700;
      }
    }

    .menu-guide-list{
      margin-top: 14.8rem;
      margin-bottom: 11.6rem;

      display: flex;
      justify-content: space-between;

      li{
        width: 25%;
        max-width: 34rem;
        padding: 0 6.8rem 4.8rem 6.8rem;
        
        .icon{
          width: 14rem;
          height: 14rem;
        }

        h4{
          padding-top: 5rem;
          padding-bottom: 3rem;
          @include style_font(700, 2.4rem, 1, $commu_cyan_01);
        }

        p{
          @include style_font(400, 1.6rem, 1.6em, $font_color-212121_grey);
        }

        button{
          transition: transform 0.3s;
        }
      }
    }
    
    .btn-rounded-03{
      padding: 1.5rem 3.5rem 1.5rem 5.5rem;
      @include style_font(700, 2.2rem, 1.5em, $white);
      
      .icon{
        height: 3rem;
        background-image: none;
      }
    }
  }

  // 세 개의 게시판 - 레이아웃이 같음
  .community-board{
    position: relative;
    padding-top: 9.6rem;
    padding-bottom: 13.2rem;

    h3{
      @include style_font(700, 4rem, 1, $font_color-212121_grey);
      padding-bottom: 7.5rem;
      text-align: center;
    }

    .board-wrap{
      padding: 4.6rem;
      box-shadow: 0px 15px 40px rgba(45, 52, 74, 0.05);
    }

    .cont-ul{
      margin-bottom: 4.8rem;
    
      li a{
        padding-left: 2rem;
      }
    
      li:last-child{
        border-bottom: none;
      }
    }
  }

  // 오픈소스 배경
  .community-board.of{
    background-color: $bg_gray_f;
    z-index: 0;
    &::after{
      position: absolute;
      content: '';
      width: 1076px;
      height: 100%;
      background-image: url($img_path_to_pages + 'res/bg_0111-1.svg');
      right: 0;
      top: 0;
      z-index: -1;
    }
  }
  // 사용 후기 배경
  .community-board.ua{
    background-color: $commu_cyan_01;
    h3{
      color: white;
    }
    z-index: 0;
    &::after{
      position: absolute;
      content: '';
      width: 1680px;
      height: 100%;
      background-image: url($img_path_to_pages + 'res/bg_0111-2.svg');
      right: 0;
      top: 0;
      z-index: -1;
    }
  }
  // 묻고 답하기 배경
  .community-board.qa{
    background-color: $bg_gray_f;
    z-index: 0;
    &::before{
      position: absolute;
      content: '';
      width: 778px;
      height: 100%;
      background-image: url($img_path_to_pages + 'res/bg_0111-3_1.svg');
      left: 0;
      top: 0;
      z-index: -1;
    }
    &::after{
      position: absolute;
      content: '';
      width: 599px;
      height: 100%;
      background-image: url($img_path_to_pages + 'res/bg_0111-3_2.svg');
      background-repeat: no-repeat;
      right: 0;
      top: 0;
      z-index: -1;
    }
  }
}

// * 🦄️Interaction
.main-osp0111{
  .community-main{

    h1{
      transition: transform 0.6s;
      animation-name: moveUp, fadeIn;
      // animation-duration: 0.9s, 0.9s;
      animation-duration: 1s, 1s;
      animation-timing-function: ease;
    }

    .menu-guide-list{
        li{
          transition: all 0.3s;
          transform: translateY(4rem);
          opacity: 0;
        }

        li:nth-child(1){
          @include animation(0s, 1s, cardUp, cubic-bezier(0.25, 0, 0.40, 1));
        }
        li:nth-child(2){
          @include animation(0.25s, 1s, cardUp, cubic-bezier(0.25, 0, 0.40, 1));
        }
        li:nth-child(3){
          @include animation(0.5s, 1s, cardUp, cubic-bezier(0.25, 0, 0.40, 1));
        }
        li:nth-child(4){
          @include animation(0.75s, 1s, cardUp, cubic-bezier(0.25, 0, 0.40, 1));
        }
    }
  }
}

@include mobile-max{
  .main-osp0111{

    .community-main{
      padding-top: 4rem;
      padding-bottom: 4rem;

      h1{
        width: 50%;

        font-size: 1.6rem;
        line-height: 1.5em;
      }
      
      .menu-guide-list__wrapper{
        position: relative;
        // display: flex;
        align-items: center;
        justify-content: center;
        overflow: scroll;
        overflow: hidden;
        
        .menu-guide-list{
          width: max-content;
          margin-top: 3.4rem;
          margin-bottom: 4.7rem;
          column-gap: 5rem;

          li{
            display: flex;
            justify-content: center;
            width: 100%;
            max-width: 100%;
            padding: 0 5rem;

            button{
              width: 100%;
            }

            .icon{
              width: 12rem;
              height: 12rem;
            }

            h4{
              padding-top: 1rem;
              padding-bottom: 2rem;

              font-size: 1.5rem;
            }

            p{
              width: 20.4rem;
              font-size: 1.3rem;
            }
          }

        }
      }

      .btn-rounded-03{
        font-size: 1.4rem;
        padding: 0;
        width: 22.5rem;
        height: 3.6rem;

        .icon{
          width: 1.8rem;
          height: 1.8rem;
        }
      }
    }

    .community-board{
      padding-top: 4rem;
      padding-bottom: 4rem;
      user-select: none;
      
      h3{
        font-size: 1.6rem;
        padding-bottom: 3.4rem;
      }

      .board-wrap{
        padding: 2rem 0;
        padding-bottom: 2.3rem;
      }
    }
  }
}