// 🏠️ OSP-5112

@import '../../modules';

.main-osp5112.fixed{
  margin-top: 25.2rem;
}

.main-osp5112{
  position: relative;
  text-align: center;
  padding-bottom: 12rem;
  
  // 티맥스 구름 특징 - 메인
  .tmaxgr-feature--main{
    padding-top: 8rem;
    padding-bottom: 19rem;

    h1{
      @include style_font(900, 4rem, 1, #212121);
      padding-bottom: 4rem;
    }

    .main-title-cont{
      @include style_font(400, 2.8rem, 1.8, #3c3c3c);
      padding-bottom: 10.6rem;
    }

    // 이미지
    .img-tmaxgr-feature{
      display: inline-block;
      width: 1222px;
      height: 683px;
      background-image: url($img_path_to_pages + 'product/img_5112-1.png');
    }
    
    // 메인 - 특징 리스트
    .feature-list{
      display: flex;
      justify-content: space-between;
      padding-top: 15.7rem;
      width: 1108px;

      li{
        width: 33.33%;
        // padding: 0 3rem;

        // icon 공통 속성
        .icon-tmaxgr-feature-sec{
          width: 9rem;
          height: 9rem;
          background-image: url($img_path_to_pages + 'product/icon-tmaxgr-feature-sec.svg');
        }
        .icon-tmaxgr-feature-vos{
          width: 14.6rem;
          // height: 7.5rem;
          height: 9rem;
          background-image: url($img_path_to_pages + 'product/icon-tmaxgr-feature-vos.svg');
          background-position: center;
        }
        .icon-tmaxgr-feature-uiux{
          width: 9rem;
          height: 9rem;
          background-image: url($img_path_to_pages + 'product/icon-tmaxgr-feature-uiux.svg');
        }

        h5{
          @include style_font(700, 2.8rem, 1, $font_color-212121_grey);
          padding-top: 4.9rem;
          padding-bottom: 3.2rem;
        }
        
        p{
          @include style_font(400, 18px, 1.6em, $font_color-212121_grey);
        }
      }
    }
  }

  // 티맥스 구름 특징 - 제품소개 (특장점)
  .tmaxgr-feature--info{
    padding-top: 16rem;
    background-color: $bg_gray_f;

    // 강력한 보안 환경
    .info-sec{
      position: relative;
      padding-top: 1rem;
      padding-bottom: 22rem;
      text-align: center;

      h3{
        @include style_font(700, 4rem, 1, #212121);
        padding-bottom: 8.9rem;
      }

      .info-sec__container{
        position: relative;
        display: inline-block;
        
        .sec-title{
          display: flex;
          justify-content: center;
          align-items: center;
          height: 56.9rem;
          margin: 0 auto;

          h4{
            width: 1372px;
            height: 483px;
            padding-top: 19.6rem;
            background-color: white;
            border-radius: 50rem;

            .basic{
              @include style_font(400, 3.2rem, 1, #212121);
              padding-bottom: 2rem;
            }

            .highlight{
              font-size: 3.2rem;
              font-weight: 700;
            }
          }
        }

        .sec-cont{
          position: absolute;
          top: 0;
          width: 100%;
          height: 100%;

          .sec-list{
            display: inline-flex;
            flex-wrap: wrap;
            justify-content: center;
            align-content: space-between;
            width: 96rem;
            height: 100%;

            gap: 2.2rem;

            li{
              width: 30.3rem;
              height: 17.2rem;
              padding-top: 4.5rem;
              color: white;
              background-color: $azure_emp_02;
              border-radius: 5rem;

              h5{
                @include style_font(700, 2.4rem, 1, white);
                padding-bottom: 2rem;
              }

              p{
                @include style_font(400, 1.4rem, 1, white);
                padding-bottom: 1.2rem;
              }
            }
          }
        }
      }
    }

    // OS 중앙관리 솔루션
    .info-sol{
      padding-top: 1rem;
      padding-bottom: 22rem;

      h3{
        @include style_font(700, 4rem, 1, #212121);
        padding-bottom: 4rem;
      }

      p{
        @include style_font(400, 2.8rem, 1.6em, #666666);
      }

      .info-sol__container{
        position: relative;
        margin: 0 auto;
        margin-top: 8rem;
        
        .sol-title{
          position: absolute;
          top: 0;

          width: 100%;
          height: 48rem;

          text-align: center;
          display: flex;
          align-items: center;
          justify-content: center;

          h4{
            @include style_font(900, 4rem, 1, white);
            // position: relative;
            position: absolute;

            display: inline-flex;
            justify-content: space-between;
            flex-direction: column;
            align-items: center;
            
            width: 53rem;
            height: 26rem;

            padding: 6rem 0;
            
            background-color: $azure_emp_01;
            border-radius: 6rem;

            // gpms
            .round{
              font-size: 2.4rem;
              padding: 2rem 3rem;
              // margin-top: 3.4rem;
              width: max-content;


              color: $azure_emp_01;
              background-color: white;
              border-radius: 6rem;
            }
          }
        }
        
        .sol-cont{
          .sol-list{
            max-width: 1372px;
            height: 48rem;

            display: inline-flex;
            flex-wrap: wrap;
            justify-content: space-between;
            align-content: space-between;

            li{
              width: 47rem;
              height: 20rem;
              padding-top: 4.3rem;

              background-color: white;
              border-radius: 8rem;
  
              h5{
                @include style_font(700, 2.8rem, 1, $azure_emp_01);
                padding-bottom: 1em;
              }
              p{
                @include style_font(400, 1.8rem, 1, $font_color-212121_grey);
                padding-bottom: 1em;
              }
            }

            // 정렬 
            .--right{
              text-align: right;
              padding-right: 15.8rem;
            }
            .--left{
              text-align: left;
              padding-left: 15.8rem;
            }
          }
        }

        .sol-svg{
          min-width: 1161px;
          height: 390px;
        }
      }
    }
  }

  // 티맥스 구름 특징 - 사업
  .tmaxgr-feature--business{
    padding: 11.6rem 0;
    background: linear-gradient(270deg, #44BCFF 0%, #4BA5FF 100%);
    
    // 사업 - 리스트
    .business-list{
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      gap: 2.3rem;

      li{
        width: 46.4rem;
        height: 61.2rem;
        padding-top: 8.4rem;

        text-align: center;

        border-radius: 3rem;
        background-color: white;

        .icon{
          width: 18rem;
          height: 18rem;
          border-radius: 50%;
          // 아이콘 들
          &.icon-tmaxgr-business-fi{
            background-image: url($img_path_to_pages + 'product/icon-tmaxgr-business-fi.svg');
          }
          &.icon-tmaxgr-business-co{
            background-image: url($img_path_to_pages + 'product/icon-tmaxgr-business-co.svg');
          }        
          &.icon-tmaxgr-business-ent{
            background-image: url($img_path_to_pages + 'product/icon-tmaxgr-business-ent.svg');
          }
        }

        h6{
          @include style_font(700, 3.2rem, 1, $azure);
          position: relative;
          
          padding-top: 7.4rem;
          padding-bottom: 3rem;
        }

        // 파란 선 
        .bar{
          width: 2rem;
          height: 4px;
          background-color: $azure;
          
          border-radius: 1rem;
        }

        p{
          padding-top: 3rem;
          @include style_font(400, 2.2rem, 1.6em, #212121);
        }
      }
    }
  }

  // 티맥스 구름 특징 - 모델
  .tmaxgr-feature--model{
    padding-top: 17rem;

    h2{
      @include style_font(700, 3.6rem, 1, #212121);
      padding-bottom: 11.8rem;
    }

    // 모델 리스트
    .model-list{
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
    
      li{
        display: flex;
        flex-direction: column;

        // width: 70rem;
        width: 48.5%;
        
        box-shadow: 0px 7px 30px rgba(0, 0, 0, 0.1);
        border-radius: 50px;

        overflow: hidden;

        h6{
          @include style_font(700, 2.8rem, 1, white);
          width: 100%;
          padding: 3rem 0;
        }
        
        // ! 이미지 파일 - hmm... 
        .img{
          display: inline-block;
          width: 100%;
          // height: calc(100% - 8.8rem);
          // height: 85%;
          height: 474px;
        
          background-size: auto 436px;
          background-position: bottom;
          
          // 이미지
          &.img-tmaxgr-model-off{
            background-image: url($img_path_to_pages + 'product/img-tmaxgr-model-off.png');
          }
          &.img-tmaxgr-model-hwork{
            background-image: url($img_path_to_pages + 'product/img-tmaxgr-model-hwork.png');
          }
        }
      }

      .office h6{
        background-color: #3C3C3C;
      }
      .homework h6{
        background-color: $azure_emp_01;
      }
    }
  }

  // 티맥스 구름 특징 - 레퍼런스
  .tmaxgr-feature--ref{
    padding-top: 17rem;
    padding-bottom: 20rem;

    h2{
      @include style_font(700, 3.6rem, 1, #212121);
      padding-bottom: 11.8rem;
    }

    // 레퍼런스 리스트
    .ref-list{
      display: flex;
      justify-content: space-between;

      li{
        width: 34.2rem;
        padding: 4.2rem 2rem 2rem 2rem;

        background-color: #D4E8FB;
        border-radius: 3rem;

        text-align: center;

        h4{
          @include style_font(700, 2.2rem, 1, $azure_emp_02);
          padding-bottom: 3rem;
        }

        .ref-text{
          display: inline-flex;
          justify-content: center;
          align-items: center;
          height: 6.4rem;
          margin-bottom: 4rem;
          p{
            @include style_font(400, 1.8rem, 1.6em, $azure_emp_02);
            opacity: 0.7;
          }
        }
        
        // 이미지들
        .img{
          display: inline-block;
          width: 30.2rem;
          height: 39rem;
          
          border-radius: 2rem;
          background-color: white;
          background-size: contain;
          background-position: center;

          &.ref-img-01{
            background-image: url($img_path_to_pages + 'product/ref-img-01.svg');
          }
          &.ref-img-02{
            background-image: url($img_path_to_pages + 'product/ref-img-02.svg');
          }
          &.ref-img-03{
            background-image: url($img_path_to_pages + 'product/ref-img-03.svg');
          }
          &.ref-img-04{
            background-image: url($img_path_to_pages + 'product/ref-img-04.svg');
          }
        }


      }
    }
  }


  // 배너 - 구름 커뮤니티
  .banner-commu{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    
    height: 22.2rem;

    padding: 0 16.2rem 0 8.3rem;
    margin-top: 18.6rem;
    margin-bottom: 3rem;

    border-radius: 3rem;
    background: linear-gradient(90deg, #00BAE2 0%, #00E0D3 100%);

    overflow: hidden;

    // 배경
    z-index: 0;
    &::before{
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      
      width: 100%;
      height: 100%;

      background-image: url($img_path_to_pages + 'banner/img_banner-commu.png');
      // background-size: 53% 100%;
      background-size: 76.2rem 100%;
      background-position: right;
      background-repeat: no-repeat;

      z-index: -1;
    }

    // 타이틀
    .title{
      text-align: left;

      h5{
        @include style_font(700, 3.6rem, 1, white);
        padding-bottom: 1.6rem;
      }
      p{
        @include style_font(400, 2.4rem, 1.4em, white);
      }
    }

    // 버튼 
    .btn{
      width: 280px;
      height: 76px;
    }

  }
}


// 문구 - 알림
.notice-text{
  width: 1440px;
  margin: 0 auto;
  margin-bottom: 18.6rem;


  height: 17rem;
  // padding: 3.5rem 0;

  border-top: 1px solid #DDDDDD;
  border-bottom: 1px solid #DDDDDD;

  display: inline-flex;
  justify-content: center;
  align-items: center;

  p{
    @include style_font(400, 2rem, 2, #212121);
    a{
      font-weight: 700;
    }
  }
}


// * 👽️responsive
@include mobile-max{

  // 문구 - 알림
  .notice-text{
    // padding: 0 2rem;
    width: calc(100% - 4rem);
    height: max-content;


    p{
      padding: 5rem 4rem;
      font-size: 1.2rem;
    }
  }

  // * 🦄️Interaction - mobile
  .main-osp5112.fixed{
    margin-top: 13.1rem;
  }
  .--pcview{
    display: none;
  }


  .main-osp5112{
    padding-bottom: 0;

    // 티맥스구름 - 메인
    .tmaxgr-feature--main{
      padding-top: 1.3rem;
      padding-bottom: 7.3rem;
      
      h1{
        font-size: 1.8rem;
        padding-bottom: 2rem;
      }

      .main-title-cont{
        font-size: 1.3rem;
        padding-bottom: 5.2rem;

      }

      .img-tmaxgr-feature{
        width: 100%;
        height: 19rem;
        background-size: contain;
        background-position: center;
      }

      .feature-list{
        flex-direction: column;
        width: 100%;
        align-items: center;
        padding-top: 5.3rem;

        gap: 6.5rem;

        li{
          width: 100%;

          .icon-tmaxgr-feature-sec{
            width: 4.5rem;
            height: 4.5rem;
          }
          .icon-tmaxgr-feature-vos{
            width: 7.8rem;
            height: 4.5rem;
          }
          .icon-tmaxgr-feature-uiux{
            width: 4.5rem;
            height: 4.5rem;
          }

          h5{
            font-size: 1.6rem;
            padding-top: 2rem;
            padding-bottom: 1.6rem;
          }

          p{
            font-size: 1.2rem;
          }
        }
      }
    }

    // 티맥스구름 - 제품소개 (특장점)
    .tmaxgr-feature--info{
      padding: 3.8rem 3rem 0 3rem;

      // 강력한 보안 환경
      .info-sec{
        padding-bottom: 8rem;

        h3{
          font-size: 1.8rem;
          position: absolute;
          top: 6.3rem;
          width: 100%;
          z-index: 9;
        }

        .info-sec__container{
          width: 100%;

          .sec-title{
            height: auto;

            h4{
              width: 100%;
              height: 70.9rem;
              // padding-top: 6.3rem;
              padding-top: 10.4rem;

              
              border-radius: 20rem;
              
              .basic{
                font-size: 1.4rem;
                padding-bottom: 1rem;

              }

              .highlight{
                font-size: 1.4rem;
              }
            }
          }

          .sec-cont{
            height: auto;
            top: 17.8rem;
            padding:  0 3.8rem;
            
            .sec-list{
              width: 100%;
              gap: 0;
              background-color: $azure_emp_02;
              border-radius: 3rem;
              padding: 1rem 0 0.6rem 0;

              
              li{
                width: 100%;
                height: max-content;
                padding: 2.4rem 0;

                margin: 0 2rem;
                background-color: transparent;
                border-radius: 0%;
                border-bottom: 1px solid  rgba(255, 255, 255, 0.1);

                h5{
                  font-size: 1.4rem;
                }

                p{
                  font-size: 1rem;
                  padding-bottom: 4px;
                }
              }

              li:last-child{
                border-bottom: 0;
              }
            }
          }
        }
      }

      // OS 중앙관리 솔루션
      .info-sol{
        padding-top: 3.3rem;
        padding-bottom: 5.8rem;

        h3{
          font-size: 1.8rem;
          padding-bottom: 2.3rem;
        }

        p{
          font-size: 1.4rem;
        }

        .info-sol__container{
          margin-top: 4rem;

          .sol-title{
            padding: 0 6.8rem;
            height: 10.9rem;
            
            h4{
              width: 100%;
              max-width: 22.4rem;
              height: 100%;
              padding: 2.8rem 0;
              
              font-size: 1.6rem;

              border-radius: 3rem;

              .round{
                padding: 6px 1.3rem;
                font-size: 1.2rem;
              }
            }
          }

          .sol-cont{
            
            .sol-list{
              margin-top: 5rem;
              border-radius: 5rem;
              background-color: white;
              height: auto;
              padding: 6.4rem 2.8rem 0.4rem 2.8rem;

              li{
                width: 100%;
                height: max-content;
                padding: 2.4rem 0;

                background-color: transparent;
                border-bottom: 1px solid #F4F4F4;
                border-radius: 0;

                h5{
                  font-size: 1.4rem;
                  padding-bottom: 1em;
                }
                p{
                  font-size: 1rem;
                  padding-bottom: 1em;
                }
              }

              li:last-child{
                border-bottom: none;
              }

              .--right, .--left{
                text-align: center;
                padding-right: 0;
                padding-left: 0;
              }
            }
          }



        }
      }
    }

    // 티맥스 구름 특징 - 사업 (활용분야)
    .tmaxgr-feature--business{
      padding: 5rem 4rem;

      // 사업 - 리스트
      .business-list{
        row-gap: 2rem;

        li{
          width: 100%;
          height: max-content;
          padding: 4.5rem 0;

          .icon{
            width: 8rem;
            height: 8rem;
          }

          h6{
            font-size: 2rem;
            padding: 4.2rem 0 2rem 0;
          }
          .bar{
            width: 1.4rem;
            height: 3px;
          }
          p{
            font-size: 1.4rem;
            padding-top: 2rem;
          }

        }
      }
    }

    // 티맥스 구름 특징 - 모델
    .tmaxgr-feature--model{
      padding-top: 7rem;

      h2{
        font-size: 1.8rem;
        padding-bottom: 4rem;
      }

      .model-list{
        padding: 0 4rem;
        row-gap: 3rem;

        li{
          width: 100%;
          border-radius: 1rem;

          h6{
            font-size: 1.4rem;
            padding: 1.3rem 0;
          }

          .img{
            height: 21.5rem;
            background-size: auto 18.4rem;
            background-position: center;
          }
        }
      }
    }

    // 티맥스 구름 특징 - 레퍼런스
    .tmaxgr-feature--ref{
      padding-top: 7.6rem;
      padding-bottom: 8.6rem;

      h2{
        font-size: 1.8rem;
        padding-bottom: 4rem;
      }

      .ref-list{
        padding: 0 4rem;
        flex-direction: column;
        row-gap: 2rem;

        li{
          width: 100%;

          h4{
            font-size: 1.6rem;
            padding-bottom: 1.4rem;
          }

          .ref-text{
            height: 5rem;
            margin-bottom: 2.6rem;
            p{
              font-size: 1.4rem;
            }

          }

          .img{
            width: 100%;
            height: 31.3rem;
          }
        }
      }

    }


    // 배너 - 구름 커뮤니티
    .banner-commu{
      height: 19rem;
      margin-top: 9.6rem;
      margin-bottom: 2rem;

      padding: 2.6rem 4rem 3rem 4rem;
      border-radius: 0%;
      flex-direction: column;

      align-items: flex-start;

      &::before{
        background-size: 100%;
        background-position: bottom;

      }

      .title{

        h5{
          font-size: 1.6rem;
        }
        p{
          font-size: 1.4rem;
        }

      }

      .btn{
        align-self: flex-end;

        width: 14.2rem;
        height: 4rem;
        font-size: 1.4rem;
      }
    }
  }
}