@import '../../modules';


.main-osp7126{

  .width1218{
    width: 1218px;
    margin: 0 auto;
  }

  .background-01{
    @include style_background-01(1194px, -321px, -291px, 900px, -246px, -405px);
  }

  .products-list{
    position: relative;
    padding-top: 9.6rem;
    
    h2{
      line-height: 1.6em;
      padding-bottom: 9.6rem;

      strong{
        color: $azure;
      }
    }

    .products{
      padding-bottom: 18rem;

      .product{
        display: flex;
        justify-content: space-between;
        align-items: center;

        padding: 4.8rem 6.4rem 4.6rem 6.4rem;
        margin-bottom: 5rem;

        background-color: $white;
        box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.05);
        border-radius: 30px;
      }
      
      // 제품 정보 
      .product-infor{
        text-align: left;
        flex: 2 2 auto;
        h4{
          height: 4rem;
          @include style_font(700, 3rem, 1, black);
        }
        p{
          @include style_font(400, 1.8rem, 2em, #212121);
          padding-top: 1.7rem;
        }
      }
      
      .tmax-gr{
        // 제품 정보
        .product-infor{
          p{
            width: 60rem;
            padding-bottom: 3.6rem;
          }
        }

        // 제품 상세 스펙
        .tmaxgr-spec{
          @include style_font(700, 1.5rem, 1, #91989D);
          
          .spec-list{
            display: inline-flex;
            flex-wrap: wrap;
            width: 75%;
            padding-left: 3.6rem;

            li{
              position: relative;
              width: 50%;
              width: 30rem;
              padding-bottom: 1.4rem;
              padding-left: 1.4rem;
              // 파란 점
              &::after{
                position: absolute;
                content: '';
                width: 5px;
                height: 5px;
                background-color: $azure;
                left: 0;
                top: 5px;
                border-radius: 50%;
              }
              // 스펙명
              span.spec-tit{
                width: 5.6rem;
                font-weight: 700;
              }
              span{
                letter-spacing: 0;
                font-weight: 400;
              }
            }
          }
        } 

      }

      // 버튼들 
      .btns-download{
        display: flex;
        flex-direction: column;
        row-gap: 2.4rem;

        .btn{
          display: inline-flex;
          justify-content: space-between;
          align-items: center;
          width: 20.8rem;
          padding: 1.1rem 1.9rem 1.1rem 2.4rem;
        }

        .btn-rounded-03{
          @include style_font(500, 1.8rem, 1.2, white);

          .icon-down-svg{
            path{
              fill: $white;
            }
          }
        }
        
        .icon.icon-down-svg{
          width: 2.4rem;
          height: 2.4rem;
        }
        
        .icon.icon-open_in_new{
          width: 1.6em;
          height: 2.4rem;
          path{
            fill: $white;
          }
        }
      }
    }
  }
}

@include mobile-max{
  .main-osp7126{

    .width1218{
      width: 100%;
    }

    .products-list{
      padding-top: 3.4rem;

      h2{
        padding-bottom: 3.5rem;
      }

      .products{
        padding-bottom: 4rem;

        .product{
          flex-direction: column;
          align-items: flex-start;
          margin-bottom: 2rem;
          border-radius: 1.5rem;
          padding: 3rem 2rem 4rem 2rem;
        }
  
        .product-infor{
  
          h4{
            height: max-content;
            font-size: 1.6rem;
          }

          p{
            padding-top: 8px;
            font-size: 1.3rem;
          }
        }

        .tmax-gr{
          .product-infor{
            p{
              width: 100%;
              padding-bottom: 1.5rem;
            }
          }

          .tmaxgr-spec{
            .spec-cate{
              display: block;
              margin: 1.5rem 0 2rem 0;
            }

            .spec-list{
              padding-left: 0;
              width: 100%;
            }
          }
        }

        .btns-download{
          padding-top: 3rem;
          flex-direction: row;
          column-gap: 8px;

          // 설치 버튼 숨김
          .--install{
            display: none;
          }

          .btn{
            width: 15rem;
            height: 4rem;

            span{
              font-size: 1.4rem;
            }
          }

          .icon.icon-down-svg,
          .icon.icon-open_in_new{
            width: 1.8rem;
            height: 1.8rem;
          }
        }
      }
    }
  }
}