@import 'btns.navi';

// * 버튼 - basic theme
.btn{
  display: inline-flex;
  transition: all 0.3s;
}

.btn-basic-01{
  padding: 1.1rem 2rem;
  height: 4.2rem; 
  border-radius: 5px;
  &.--d{
    color: $font_color-B0B0B0_grey;
    border: 1px solid $font_color-B0B0B0_grey;
  }
  &.--a{
    color: #666666;
    border: 1px solid #666666;
    &:active{
      color: #212121;
      border: 1px solid #212121;
    }
  }
}

.btn-basic-02{
  display: inline-flex;
  padding: 1.1rem 2rem;
  height: 4.2rem; 
  border-radius: 5px;
  &.--d{
    background-color: #B0B0B0;
    color: white;
  }
  &.--a{
    color: white;
    background-color: $azure;
    &:hover{
      background-color: $azure_emp_01;
    }
    &:active{
      background-color: $azure_emp_02;
    }
  }

}

.btn-rounded-01{
  @include style_font(500, 1.8rem, 1, $azure);
  padding: 1.5rem 3rem 1rem 3rem;
  background: white;
  border: 1px solid $azure;
  border-radius: 4rem;
  
  .icon-down-svg svg{
    width: 2.4rem;
    height: 2.4rem;
    path{
      fill: $azure;
    }
  }
  &:hover{
    color: $azure_emp_01;
    border: 1px solid $azure_emp_01;
    .icon-down-svg svg path{
      fill: $azure_emp_01;
    }
  }
  &:active{
    color: $azure_emp_02;
    border: 1px solid $azure_emp_02;
    .icon-down-svg svg path{
      fill: $azure_emp_02;
    }
  }

}

.btn-rounded-02{
  @include style_font(700, 2.2rem, 1, $azure);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 1.5rem 3rem;

  background: white;
  border-radius: 4rem;
  transition: all 0.3s;
  color: $azure;
  &:hover{
    color: $azure_emp_01;
  }
  &:active{
    color: $azure_emp_02;
  }
}


.btn-rounded-02.commu{
  color: $commu_cyan_01;
  &:hover{
    color: $commu_cyan_02;
  }
  &:active{
    color: $commu_cyan_03;
  }
}

.btn-rounded-03{
  @include style_font(700, 2.2rem, 1.2, white);
  border-radius: 4rem;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  // height: 6.4rem;
  // padding: 1.65rem 3.5rem 1.5rem 5.5rem;
  background-color: $azure;
  transition: background-color 0.3s;
  i.icon{
    // width: 3.4rem;
    // height: 3.4rem;
    margin-left: 1rem;
    // vertical-align: -0.66em;
  }
  &:hover{background-color: $azure_emp_01;}
  &:active{background-color: $azure_emp_02;}
}

// 추후에 상태 업데이트 예정 
.btn-rounded-03-grad{
  @include style_font(500, 1.8rem, 1.2, white);
  background: linear-gradient(270deg, #44BCFF 0%, #4BA5FF 100%);
  border-radius: 4rem;
  .icon-down-svg svg{
    width: 2.4rem;
    height: 2.4rem;
    path{
      fill: white;
    }
  }
}

// * 버튼 - 메뉴 박스 토글 (점 세 개)
.btn-box-toggle{
  i.icon{
    width: 2.4rem;
    height: 2.4rem;
    opacity: 0.3;
    transition: opacity 0.3s;
  }
  &:hover{
    i.icon{
      opacity: 0.6;
    }
  }
  &:active{
    i.icon{
      opacity: 1;
    }
  }
}  

// * 버튼 - 더보기나 상세페이지 이동
.btn-more{ 
  font-weight: 500;
  padding: 1rem 0;
  color: $color-0082F3_btn;
  text-align: center;

  .icon{
    margin-left: 6px;
  }

  &:hover{
    color: $azure_emp_01;
    
    .icon{
      transition: transform 0.6s;
      
      @include animation(0s, 0.8s, moveSideToSide);
      animation-iteration-count: infinite;
    }
  }
  &:active{color: $azure_emp_02;}
}

.btn-more.commu{ 
  @include style_font(500, 1.8rem, 1, $commu_cyan_03);
  // display: inline-block;
  color: $commu_cyan_01;
  &:hover{color: $commu_cyan_02;}
  &:active{color: $commu_cyan_03;}
}

// * 버튼 - commu theme

.btn-basic-02.commu{
  &.--a{
    background-color: $commu_cyan_01;
    &:hover{
      background-color: $commu_cyan_02;
    }
    &:active{
      background-color: $commu_cyan_03;
    }
  }

}
.btn-rounded-01.commu{
  @include style_font(500, 1.8rem, 1, $commu_cyan_01);
  background: #FFFFFF;
  border: 1px solid $commu_cyan_01;
  border-radius: 4rem;
  .icon-down-svg{
    vertical-align: middle;
    width: 2.4rem;
    height: 2.4rem;
    margin-left: 1rem;
    svg path{
      fill: $commu_cyan_01;
    }
  }
  &:hover{
    color: $commu_cyan_02;
    border: 1px solid $commu_cyan_02;
    i.icon svg path{
      fill: $commu_cyan_02;
    }
  }
  &:active{
    color: $commu_cyan_03;
    border: 1px solid $commu_cyan_03;
    i.icon svg path{
      fill: $commu_cyan_03;
    }
  }
}


.btn-rounded-03.commu{
  @include style_font(500, 1.8rem, 1, white);
  padding: 1.5rem 3rem;
  background-color: $commu_cyan_01;
  &:hover{
    background: #06A1CB;
  }
  &:active{
    background: $commu_cyan_03;
  }
}