@import '../../modules';



// 밑줄 그여진 부분 
.highlight{
  position: relative;
  color: $azure;

  &::after{
    position: absolute;
    left: 0;
    bottom: -1rem;
    content: '';
    width: 100%;  
    height: 2rem;

    background-color: $azure;
    opacity: 0.2;
  }
}

// 숫자 아이콘
.number-icon{
  position: absolute;
  left: 3rem;
  width: 3.2rem;
  height: 3.2rem;
  text-align: center;
  padding-top: 0.5rem;
  @include style_font(700, 2rem, 1, white);
  background-color: $commu_cyan_03;
  border-radius: 50%
}


.main-osp5113{
  text-align: center;
  padding-bottom: 17.8rem;

  // 티맥스 구름SD 특징 - 메인 (제품 소개)
  .tmaxgrsd-feature--main{
    position: relative;
    padding-top: 8.4rem;

    // 메인 일러스트
    .illust-tmaxgrsd-main{
      margin-bottom: 16rem;

      // 가운데 앉아있는 남자
      .illust-man{
        position: relative;
        width: 62.3rem;
        height: 51.2rem;

        background-image: url($img_path_to_pages + 'product/svg_5113-1.svg');
        background-size: 100% 100%;
        background-repeat: no-repeat;
        background-position: bottom;
      }

      // 말풍선 공통
      .bubble{
        position: absolute;
        padding: 4.0rem 0 8.2rem 6.3rem ;
        text-align: left;
        @include style_font(400, 3.2rem, 1.6em, white);
        
        strong{
          display: block;
          font-weight: 700;
        }

        background-size: 100%;
        background-repeat: no-repeat;
        
        // 왼쪽 말풍선
        &.--bl{
          left: -32.7rem;
          top: -4.4rem;
  
          width: 53rem;
          background-image: url($img_path_to_pages + 'product/svg-bubble-bl.svg');
          // background-position: top;
        }
  
        // 오른쪽 말풍선
        &.--mt{
          top: -10.1rem;
          right: -12.9rem;
          width: 46.5rem;
          background-image: url($img_path_to_pages + 'product/svg-bubble-mt.svg');
        }
      }

    }

    // 메인 문구
    h1{
      @include style_font(900, 4rem, 1, $font_color-212121_grey);
      letter-spacing: 0;
    }

    // 메인 특징들
    .main-feature{
      margin-top: 17.6rem;

      .feature-list{
        display: flex;
        justify-content: space-between;
      
        li{
          width: 41.4rem;

          box-shadow: 0 40px 40px rgba(0, 0, 0, 0.04);
          border-radius: 2rem;
          background-color: white;

          overflow: hidden;
          
          h4{
            @include style_font(700, 2.4rem, 1, white);
            padding: 3.2rem 0;
            background-color: $azure;
          }

          .feature-depth2{
            text-align: left;
            padding: 3.7rem 0 4rem 6rem;

            p{
              position: relative;
              @include style_font(400, 1.8rem, 2, $black_0);

              &::after{
                position: absolute;
                content: '';
                left: -1.8rem;
                top: 1.4rem;
                width: 6px;
                height: 6px;
                border-radius: 50%;
                background-color: #007DF1;
              }
            }
            
          }
        }
      }
    }

    // 메인 질문들
    .main-qna{
      margin-top: -15.6rem;
      padding-top: 35.3rem;
      padding-bottom: 22.7rem;

      background-color: $bg_gray_f;
      // height: 1886px;

      .width1350{
        width: 1350px;
        margin: 0 auto;
      }

      // qna 타이틀
      .qna-title{
        position: absolute;
        width: 100%;
        top: -3.8rem;

        h3{
          @include style_font(700, 2.8rem, 1, white);
          width: 30.2rem;
          // height: 7.6rem;
          padding: 2.4rem 0;
          margin: 0 auto;

          background-color: $commu_cyan_03;
          border-radius: 2rem;
        }
      }

      // qna 리스트
      .qna-cont{
        position: relative;
        background-color: #E6EFF1;
        border-radius: 3rem;
        box-shadow: 0 7px #D1E1E4;

        .qna-list{
          padding: 10.2rem 5rem;

          li{
            position: relative;
            padding: 4rem 8.4rem ;
            margin-bottom: 3rem;

            text-align: left;

            background-color: white;
            border-radius: 1rem;

            h4{
              @include style_font(700, 2.4rem, 1, $commu_cyan_03);
              padding-bottom: 3.2rem;
            }

            p{
              @include style_font(400, 1.8rem, 1.6, #3c3c3c);
              // padding-bottom: 0.4em;
              strong{
                font-weight: 700;
              }
            }
          }
          
        }
      }
    }
  }

  // 티맥스 구름SD 특징 - 사업
  .tmaxgrsd-feature--business{
    padding: 11.6rem 0;
    background: linear-gradient(270deg, #44BCFF 0%, #4BA5FF 100%);
    
    // 사업 - 리스트
    .business-list{
      display: flex;
      justify-content: center;
      gap: 2.3rem;
      flex-wrap: wrap;

      li{
        width: 46.4rem;
        height: 61.2rem;
        // padding-top: 5.4rem;
        padding-top: 8.4rem;

        text-align: center;

        border-radius: 3rem;
        background-color: white;

        .icon{
          width: 36.4rem;
          height: 21.8rem;
          // 아이콘 들
          &.icon-tmaxgrsd-business-cu{
            background-image: url($img_path_to_pages + 'product/icon-tmaxgrsd-business-cu.svg');
          }
          &.icon-tmaxgrsd-business-sec{
            background-image: url($img_path_to_pages + 'product/icon-tmaxgrsd-business-sec.svg');
          }
          &.icon-tmaxgrsd-business-us{
            background-image: url($img_path_to_pages + 'product/icon-tmaxgrsd-business-us.svg');
          }
        }

        h6{
          @include style_font(700, 3.2rem, 1, $azure);
          padding-top: 8.5rem;
          padding-bottom: 4rem;
        }
        
        p{
          @include style_font(400, 2.2rem, 1.6em, #212121);
        }
      }
    }
  }

  // 티맥스 구름SD 특징 - 레퍼런스
  .tmaxgrsd-feature--ref{
    padding-top: 20rem;

    .width1340{
      width: 1340px;
      margin: 0 auto;
    }

    // 레퍼런스 - 주요
    .ref-core{
      border-radius: 3rem;
      box-shadow: 0px 7px 50px rgba(0, 0, 0, 0.1);
      overflow: hidden;

      h4{
        @include style_font(700, 2.8rem, 1, white);
        padding-top: 3rem;
        padding-bottom: 3.2rem;
        background-color: #3c3c3c;
      }

      // 리스트
      .core-list{
        height: 34.8rem;
        padding: 6.7rem 8.7rem 8.5rem 8.7rem;
        display: flex;
        justify-content: space-between;
        // height: 19.6rem;
        flex-wrap: wrap;

        background-image: url($img_path_to_pages + 'product/temp_logo.png');
        background-repeat: no-repeat;
        background-position: cover;
        background-position: center;
        background-size: contain;
        background-size: 85%;
        // background-size: 100% 100%;
        
        li{
          .icon{
            // 임시
            // height: 10rem;
            // width: 24rem;
            // background-color: gainsboro;
          }
        }
      }
    }

    // 레퍼런스 - 순서
    .ref-order{
      padding-top: 17rem;
      padding-bottom: 19rem;

      h4{
        @include style_font(700, 3.6rem, 1, #212121);
        padding-bottom: 12rem;
      }

      .order-list{
        display: inline-flex;
        gap: 6.4rem;

        // 아이콘
        li{
          position: relative;
          
          .icon{
            width: 22rem;
            height: 22rem;

            &.icon-tamxgrsd-order-01{
              background-image: url($img_path_to_pages + 'product/icon-tamxgrsd-order-01.svg');
            }
            &.icon-tamxgrsd-order-02{
              background-image: url($img_path_to_pages + 'product/icon-tamxgrsd-order-02.svg');
            }
            &.icon-tamxgrsd-order-03{
              background-image: url($img_path_to_pages + 'product/icon-tamxgrsd-order-03.svg');
            }
            &.icon-tamxgrsd-order-04{
              background-image: url($img_path_to_pages + 'product/icon-tamxgrsd-order-04.svg');
            }
            &.icon-tamxgrsd-order-05{
              background-image: url($img_path_to_pages + 'product/icon-tamxgrsd-order-05.svg');
            }
          }

          p{
            @include style_font(400, 2.4rem, 1, $font_color-212121_grey);
            padding-top: 3rem;
          }
        }

        li{
          .icon-more{
            width: 1.6rem;
            height: 2.2rem;

            position: absolute;
            top: 10rem;
            right: -3.6rem;
          }
        }
      }
    }

    // 레퍼런스 - 예시
    .ref-expl{
      padding-bottom: 25rem;

      h4{
        @include style_font(700, 3.6rem, 1, #212121);
        padding-bottom: 12rem;
      }

      .expl-list{
        display: inline-flex;
        gap: 8rem;

        li{
          position: relative;
          text-align: center;
          
          // 예시 이미지
          .img{
            width: 41rem;
            height: 37.4rem;
            
            margin-bottom: 3.5rem;

            border: 1px solid #DDDDDD;
            border-radius: 1rem;

            box-shadow: 1rem 1rem #F8F8F8;
            background-size: 100.6% 100.6%;
            background-position: center;

            &.img-ref-expl-01{
              background-image: url($img_path_to_pages + 'product/img-ref-expl-01.png');
            }
            &.img-ref-expl-02{
              background-image: url($img_path_to_pages + 'product/img-ref-expl-02.png');
            }
            &.img-ref-expl-03{
              background-image: url($img_path_to_pages + 'product/img-ref-expl-03.png');
            }
          }

          // 숫자 아이콘
          .number-icon{
            position: relative;
            left: 0;
            background-color: $azure;
          }

          p{
            @include style_font(400, 2.4rem, 1.6em, $azure_emp_01);
            padding-top: 3.3rem;
          }
        }
      }
    }
  }

  .notice-text{
    margin-bottom: 18.6rem;
  }
}


// * 🦄️Interaction
.main-osp5113.fixed{
  margin-top: 25.2rem;
}


// * 👽️responsive
@include mobile-max{

  .main-osp5113{
    padding-bottom: 0;


    // 티맥스 구름SD 특징 - 메인 (제품 소개)
    .tmaxgrsd-feature--main{

      // 메인 일러스트
      .illust-tmaxgrsd-main{
        margin-bottom: 6.8rem;
        padding: 0 3rem;

        .illust-man{
          width: 100%;
          height: 24.6rem;

        }

        .bubble{
          padding: 2rem 1rem 5.4rem 2rem;
          font-size: 1.5rem;
          word-break: keep-all;

          strong{
            display: inline;
          }

          &.--bl{
            left: 0;
            width: 15.8rem;
            background-image: url($img_path_to_pages + 'product/svg-bubble-bl_m.svg');
          }

          &.--mt{
            right: 0;
            width: 15.9rem;
            background-image: url($img_path_to_pages + 'product/svg-bubble-mt_m.svg');

          }
        }
      }

      h1{
        font-size: 1.8rem;
        line-height: 1.8em;

        .highlight.--mobileview{
          display: inline-block;

          // highlight
          &::after{
            bottom: 0.4rem;
            height: 1rem;
          }
        }
      }

      .main-feature{
        margin-top: 5.5rem;

        .feature-list{
          padding: 0 3rem;
          flex-wrap: wrap;
          row-gap: 3.4rem;

          li{
            width: 100%;
            border-radius: 1rem;

            h4{
              padding: 1.3rem 0;
              font-size: 1.4rem;
            }

            .feature-depth2{
              padding: 1.7rem 2.8rem 2rem 3.7rem;

              p{
                font-size: 1.4rem;

                &::after{
                  top: 1.2rem;
                  width: 3px;
                  height: 3px;
                }
              }
            }
          }
        }
      }

      .main-qna{
        margin-top: -42.6rem;
        padding-top: 52rem;
        padding-bottom: 6.5rem;

        .width1350{
          width: 100%;
        }

        .qna-title{
          top: -1.8rem;

          h3{
            font-size: 1.4rem;
            width: 17rem;
            padding: 1.1rem 0;
            border-radius: 1rem;
          }
        }

        .qna-cont{
          .number-icon{
            width: 2rem;
            height: 2rem;
            font-size: 1.1rem;
            left: 1.5rem;
            // top: 0.2rem;

          }

          .qna-list{
            padding: 4.8rem 1.5rem 4.8rem 1.5rem;

            li{
              padding: 2.4rem 1.5rem 2rem 1.5rem;

              h4{
                font-size: 1.2rem;
                padding-left: 2.8rem;
                padding-bottom: 2rem;

                line-height: 1.4em;
              }
              p{
                font-size: 1.1rem;
              }
            }
          }
        }
      }

    }

    .tmaxgrsd-feature--business{
      padding: 5rem 4rem;

      .business-list{
        row-gap: 2rem;

        li{
          width: 100%;
          height: max-content;
          padding: 4.5rem 0;

          .icon{
            // width: 8rem;
            // height: 8rem;
            width: 100%;
            height: 15.5rem;
            background-size: contain;
            background-position: center;
            
          }

          h6{
            font-size: 2rem;
            padding: 2.8rem 0 2rem 0;
          }

          .bar{
            width: 1.4rem;
            height: 3px;
          }
          p{
            font-size: 1.4rem;
            padding-top: 2rem;
          }

        }
      }
    }

    .tmaxgrsd-feature--ref{
      padding: 5rem 0 0 0;

      .width1340{
        width: 100%;
      }

      .ref-core{
        width: calc(100% - 6rem);
        border-radius: 1rem;

        h4{
          padding-top: 1.8rem;
          padding-bottom: 1.6rem;
          font-size: 1.6rem;

        }

        .core-list{
          padding: 2.9rem 1.5rem 3.6rem 3.3rem;
          background-image: url($img_path_to_pages + 'product/temp_logos_respons.svg');
        }
      }

      // 레퍼런스 - 순서
      .ref-order{
        padding: 6rem 0 6rem 0;

        h4{
          font-size: 1.8rem;
          padding-bottom: 4.5rem;
        }

        .order-list{
          width: 100%;
          padding: 0 3rem 1.3rem 3rem;
          overflow-x: scroll;

          li{
            .icon{
              width: 10rem; 
              height: 10rem;
            }

            p{
              font-size: 1.2rem;
              padding-top: 1.2rem;
            }

            .icon-more{
              width: 1rem;
              height: 1.4rem;
              top: 5rem;
              right: -3rem;
            }
          }
        }
      }

      // 레퍼런스 - 예시
      .ref-expl{
        padding-bottom: 8.6rem;
        
        h4{
          padding-bottom: 4.5rem;
          font-size: 1.8rem;
        }

        .expl-list{
          flex-direction: column;
          padding: 0 3rem;
          row-gap: 5.4rem;

          li{
            width: 100%;

            .img{
              width: 30rem;
              height: 27.2rem;
              margin-bottom: 2rem;
            }

            .number-icon{
              position: absolute;
              width: 2rem;  
              height: 2rem;
              font-size: 1.1rem;
            }

            p{
              padding-top: 0rem;
              font-size: 1.2rem;
              padding-left: 2.4rem;
              text-align: left;
            }
            .--mobileview{
              display: block;
            }
          }
        }
      }
    }

    .notice-text{
      margin-bottom: 9.6rem;
    }
  }






  // * 🦄️Interaction - mobile
  .main-osp5113.fixed{
    margin-top: 13.1rem;
  }
}