// 작성자: 정수진C
@import '../modules';

// ! animation 추후에 정하기 
.tab-boxed-fix{
  position: relative;

  display: flex;
  justify-content: center;
  align-items: center;

  margin: 9.6rem 0;

  .tab-list{
    position: relative;

    display: flex;
    background-color: white;

    // 그냥 border주면 안맞음
    &::before{
      position: absolute;
      content: '';
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;

      border-radius: 1rem;
      border: 3px solid $grey_ec;
    }

    li{
      text-align: center;
      width: 29.3rem;
      
      z-index: 9;
      
      a{
        width: 100%;

        @include style_font(700, 1.8rem, 1, $font_color-AFAFAF_grey);
        display: inline-block;
        padding: 2rem 0;
      }
    }

    // focused 
    li.focused{
      left: 10%;
      // height: 60px;
      background: linear-gradient(270deg, #44BCFF 0%, #4BA5FF 100%);
      border-radius: 1rem;
      
      a{
        color: white;
      }
    }
  }
}


// * 🦄️Interaction - fixed
.tab-boxed-fix.fixed{
  position: fixed;
  width: 100%;
  top: 0;
  padding-top: 2rem;
  padding-bottom: 2rem;

  z-index: 999;
  margin: 0;

  background-color: white;
}




// * 바 스타일의 Tab
.tab__bar-style{
  position: relative;

  &::after{
    position: absolute;
    content: '';
    bottom: 0;
    left: 0;
    width: 100%;
    height: 3px;
    background-color: #ececec;
  }

  // 새로운 소식 있을 때,
  .alert-new{
    background-color: #F86848;
    border-radius: 50%;
    vertical-align: top;

    width: 2rem;
    height: 2rem;
    padding-top: 0.16em;
    margin-left: 6px;
    @include style_font(700, 1.4rem, 1, white);
  }

  ul{
    display: flex;
    justify-content: center;

    li{
      text-align: center;
    }

    a, button{
      position: relative;
      display: inline-block;
      @include style_font(700, 2rem, 1, $font_color-AFAFAF_grey);

      width: 220px;
      padding: 5.2rem 1em 3.8rem 1em;


      &::after{
        position: absolute;
        content: '';
        bottom: 0;
        left: 0;
        width: 100%;
        height: 5px;
        background: $azure;
        z-index: 99;
        opacity: 0;
        transition: opacity 0.3s;
      }
    }

    .focused, 
    button:hover{
      color: $azure;  
      transition: all 0.3s;
      &::after{
        opacity: 1;
      }
    }
  }
}

.tab__bar-style.commu{

  ul{
    a, button{
      &::after{
        background: $commu_cyan_01;
      }
    }

    .focused, 
    button:hover{
      color: $commu_cyan_01;  
    }
  }
}




// * 👽️responsive
@include mobile-max{
  // * Box style
  .tab-boxed-fix{
    margin: 3.4rem 0 5.7rem 0;
    padding: 0 1.5rem 0 1.5rem;

    .tab-list{
      width: 100%;

      &::before{
        border-radius: 5px;
        border: 2px solid $grey_ec;
      }

      li{
        width: 33.33%;
        
        
        a{
          font-size: 1.3rem;
          padding: 1.4rem 0;
        }
      }

      li.focused{
        border-radius: 5px;

      }

    }
  }


  // * Bar style
  .tab__bar-style{
    ul{
      width: 100%;
      justify-content: space-between;
      overflow-x: scroll;

      li{
        button{
          width: 100%;
          min-height: 5.2rem;
          height: 100%;
          line-height: 1.2;
          // display: block;
          // min-width: 9rem;
          
          word-break: keep-all;
          
          font-weight: 500;
          font-size: 1.2rem;
          padding: 1.1rem 1em 1.1rem 1em;
          &::after{
            height: 3px;
          }
        }
      }
    }

    .tab-size-2 li{
      width: 50%;
    }
    .tab-size-3 li{
      width: 33%;
    }
    .tab-size-4 li{
      width: 25%;
    }
    .tab-size-5 li{
      // width: 20%;
    }
  }
}