@import '../../modules';

// 05/17 코드 합침(4/27작업부분)

.main-osp6112{
  .promotion-news{
    padding-top: 148px;
  
    .title-banner{
      display: flex;
  
      .wrap-img{
        width: 600px;
        height: 355px;
        margin-right: 90px;
        border-radius: 10px;

        overflow: hidden;
        box-shadow: 0 20px 50px rgba(0, 0, 0, 0.1);
      }
  
      .wrap-content{
        width: calc(100% - 690px);
  
        &::before{
          content:'';
          display: block;
          width: 100px;
          height: 100px;
          margin-bottom: 19px;

          background-image: url($img_path_to_pages + 'ico_quotes.svg');
          background-size: cover;
          background-repeat: no-repeat;
        }
  
        .title{
          font-size: 24px;
          font-weight: bold;
          color: #3C3C3C;
          line-height: 36px;
        }
  
        .content{
          color: #666;  
          line-height: 28px;
          margin: 24px 0;
          font-size: 1.6rem;  
        }
  
        .date{
          font-size: 15px;
          color: #B0B0B0;
        }
      }
    }
  
    hr.line{
      margin-top: 116px;
      background-color: #D8DCDE;
      height: 1px; 
    }
  
    .wrap-item{
      margin-top: 146px;
    }
  }
    

}

// * 🦄️Interaction
// 타이틀 fadein
.promotion-news{
  .title-banner{
    .wrap-content{
      .title{
        animation-name: moveUp, fadeIn;
        // animation-duration: 0.9s, 0.9s;
        animation-duration: 0.6s, 0.6s;
        animation-timing-function: ease;
      }

      p.content{
        animation-name: moveUp, fadeIn;
        // animation-duration: 0.9s, 0.9s;
        animation-duration: 0.6s, 0.6s;
        animation-timing-function: ease;
      }
    }
  }
}


// * 👽️responsive
@include mobile-max{

  .main-osp6112{
    .promotion-news{
      position: relative;
      padding-top: 2.8rem;

      .title-banner{
        flex-direction: column;
        padding: 0 2rem;

        .wrap-img{
          box-shadow: none;
          width: calc(100% - 4rem);
          height: 19rem;

          position: absolute;
          top: 15.2rem;

        }

        .wrap-content{
          position: relative;
          width: 100%;
          
          &::before{
            width: 4.6rem;
            height: 4.6rem;
            margin: 0 auto;
          }

          .--mobileview{
            display: block;
          }
          .title{
            width: 100%;
            font-size: 1.6rem;
            line-height: 1.4em;
            text-align: center;
            padding-bottom: 3.4rem;

            span{
              display: block;
            }
          }

          .content{
            font-size: 1.4rem;
            line-height: 1.6em;
            margin: 21rem 0 8px 0;
          }

          .date{
            font-size: 1.2rem;
          }
        }
      }
      hr.line{
        margin: 4.8rem 0;
      }
      .wrap-item{
        margin-top: 0;
      }
    }
  }

}