@import '../../modules';

.main-osp6114{

  // 메인 홍보 영상
  .main-promote{
    position: relative;
    
    iframe{
      opacity: 1;
    }

    .btn-main-video{
      display: block;
      position: absolute;
      bottom: 0;
      height: 14.8rem;
      width: 100%;
      background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.5) 100%);

      .btn__container{
        display: flex;
        align-items: center;
        text-align: left;
        
        .icon{
          display: inline-block;
          width: 6.4rem;
          height: 6.4rem;
          margin-right: 2rem;
        }

        h2{
          @include style_font(700, 3rem, 1, white);
        }
      }
    }
  }

  // 홍보 영상 리스트
  .promotion-video{
    .video{
      margin-top: 146px;
  
      .media-list{
        flex-wrap: wrap;
        row-gap: 9.6rem;
        margin-bottom: 7.6rem;
  
        li{
          width: 44rem;
          
          .item-card{
            display: block;
          }
        }
      }
    }
  
    .pagination{
      margin-bottom: 9.6rem;
    }
  }
}



// * 👽️responsive
@include mobile-max{
  .main-osp6114{

    // 메인 홍보 영상
    .main-promote{
      iframe{
        height: 30rem;
      }

      .btn-main-video{
        height: 8.4rem;

        .btn__container{
          padding: 0 2rem;
          
          .icon{
            width: 2.4rem;
            height: 2.4rem;
            margin-right: 1.6rem;
          }
          
          h2{
            font-size: 1.4rem;
            line-height: 1.4em;
            font-weight: 400;
          }

        }
        
      }

    }

    // 홍보 영상 리스트
    .promotion-video{
      .video{
        margin-top: 3.4rem;

        .media-list{
          row-gap: 4rem;
          padding: 0 2rem;

          li .item-card.--video{
            .wrap-img{
              height: 17rem;
            }
          }
        }
      }

      .pagination{
        margin-bottom: 4rem;
      }
    }

  }
}