// 🏠️ OSP-4112

@import '../../modules';

.main-osp4114{
  text-align: center;

  .background-01{
    @include style_background-01(1254px, -569px, -595px, 772px, -162px, -400px );
  }

  .certificate__container{
    padding-top: 9.6rem;
    padding-bottom: 20.5rem;

    h2{
      @include style_font(700, 3.6rem, 1, #1f1f1f);
      padding-bottom: 9.4rem;
    }

    .certificate-list{
      display: flex;
      gap: 8rem;
      justify-content: center;

      li{
        padding: 6.4rem 7rem 8.7rem 7rem;

        background-color: white;
        box-shadow: 0px 3rem 5rem rgba(175, 175, 175, 0.1);
        border-radius: 3rem;

        // 아이콘
        .icon-certificate{
          width: 26rem;
          height: 25.8rem;
          margin-bottom: 8.2rem;
          background: url('../../../../public/asset/icon/icon_certificate.svg') no-repeat center;
          background-size: cover
        }

        button{
          position: relative;
          display: block;
          width: 100%;

          span{
            @include style_font(500, 2.4rem, 1, $font_color-212121_grey);
          }

          .icon{
            width: 2.6rem;
            height: 2.6rem;
            vertical-align: -0.2em;
          }
        }
      }
    }
  }

  .certificate-toggle__wrapper{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.3);

    display: flex;
    justify-content: center;
    align-items: center;

    .toggle__container{
      width: 88.8rem;
      box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.1);

      // 도구 박스
      .toggle-control{
        display: flex;
        justify-content: space-between;
        padding: 1.5rem 1.8rem 1.5rem 3.4rem;
        background-color: $azure;

        border-radius: 2rem 2rem 0 0;

        span{
          @include style_font(700, 2rem, 1.5em, white);
        }

        button{
          .icon{
            width: 3rem;
            height: 3rem;
          }
        }
      }

      // 인증서 이미지
      .toggle-certificate{
        height: 68rem;
        padding: 2rem 2rem 0 2rem;
        background: #F8F9FA;
        border-radius: 0 0 2rem 2rem;
        overflow: scroll;
      
        
        .img-certificate{
          background-color: white;
          background: linear-gradient(to top, rgba(2,0,36,1) 0%, rgba(0,212,255,1) 100%);
          width: 100%;
          height: 80rem;
        }
      }
    }

  }
}




// * 👽️responsive
@include mobile-max{
  .main-osp4114{

    .background-01{
      @include style_background-01(488px, -201px, -244px, 0, -0, -0);
    }
  

    .certificate__container{
      padding: 4rem 1.6rem 7rem 1.6rem;

      h2{
        font-size: 1.6rem;
        padding-bottom: 4rem;
      }

      .certificate-list{
        flex-direction: column;
        background-color: white;
        border-radius: 1.5rem;
        padding: 1.8rem 0;
        gap: 1.6rem;

        li{
          display: flex;
          padding: 1rem 2.4rem 1rem 1.7rem;
          align-items: center;
          box-shadow: none;


          .icon-certificate{
            width: 6rem;  
            height: 6rem;
            margin-bottom: 0;
          }

          button{
            text-align: left;
            padding-left: 1.7rem;
            width: calc(100% - 6rem);

            span{
              font-size: 1.4rem;
            }

            .icon{
              position: absolute;
              right: 0;
              width: 1.6rem;  
              height: 1.6rem;
            }
          }
        }


      }
    }

  }
}