@import '../../modules';


.width1225{
  width: 1225px;
  margin: 0 auto;
}

.main-osp4111{
  
  .about__tmaxos{
    position: relative;
    padding-top: 12.3rem;
    padding-bottom: 32rem;

    // 이미지
    .img-about_tmaxos_banner{
      width: 100%;
      height: 38.8rem;
      background: rgba($color: #000000, $alpha: 0.6);
      background-position: center;
      background-size: cover;

      background-image: url($img_path_to_pages + 'img-OSP4111-1x.png');
      z-index: -9;
    }

    h2{
      // position: absolute;
      position: relative;
      // width: 44.6rem;
      margin-top: -10rem;
      margin-left: 10.8rem;
      font-weight: 700;
      font-size: 40px;
      line-height: 1.6em;

      strong{
        color: $azure;
      }

      // 파란 아이콘
      &::before{
        position: absolute;
        content: '';
        top: -2.8rem;
        left: -2.8rem;
        width: 3rem;
        height: 3rem;
        background-size: cover;
        // background-image: url('../../../../public/asset/icon/icon_subtract.svg');
      }
    }
  }

  .about__information{
    width: 1225px;
    margin: 0 auto;

    letter-spacing: 0;

    // 공통
    .information__wrapper{
      position: relative;
      padding-top: 3.2rem;
      padding-bottom: 28rem;
      width: 100%;

      // 구분선
      &::after{
        position: absolute;
        content: '';
        top: 0;
        width: 100%;
        height: 3px;
        background-color: $grey_ec;
      }

      strong{
        @include style_font(700, 2.4rem, 1.5 black);
        display: inline-block;
        padding-bottom: 2rem;
      }

      .information-list{
        display: flex;
        justify-content: space-between;
        li{
          width: 50%;
          p{
            @include style_font(400, 2.4rem, 1.5, #666666);
            padding-top: 0.8em;
          }
        }
      }
    }


    .information__wrapper.--infor01{

      .information-list{
        li{
          width: 33.33%;
  
        }
        p{
          padding-top: 0;
          font-size: 32px;
        }
      }
    }



    .information__wrapper.--infor03{
      .information-list{

        li{
          // 분야 
          .cate-product{
            @include style_font(300, 1.8rem, 1.5, white);
            margin-top: 1em;
            display: inline-block;
  
  
            padding: 1.3rem 2rem;
            background-color: #666666;
            border-radius: 30px;
  
            vertical-align: top;
          }
  
          ul{
            display: inline-block;
            padding-left: 1.5em;
            width: 50%;
          }
        }
      }
    }
  }
}






// * 👽️responsive
@include mobile-max{
  .main-osp4111{

    .about__tmaxos{
      padding-top: 0;
      padding-bottom: 13rem;


      .img-about_tmaxos_banner{
        // position: relative;
        height: 10.4rem;
      }

      h2{
        margin-top: -4rem;
        font-size: 1.6rem;
        margin-left: 3.1rem;

        &::before{
          top: -2.4rem;
          left: -1.3rem;
          width: 1.5rem;
          height: 1.5rem;

        }
      }
    }

    .about__information{
      width: 100%;
      padding: 0 3rem 5.3rem 3rem;

      .information__wrapper{
        padding-top: 2rem;
        padding-bottom: 0rem;

        // 구분선
        &::after{
          height: 2px;
        }

        strong{
          font-size: 1.2rem;
        }

        .information-list{
          flex-direction: column;

          li{
            width: 100%;
            p{
              font-size: 1.6rem;
            }
          }
        }
      }

      .information__wrapper.--infor01{
        padding-bottom: 15.5rem;

        .information-list{
          flex-direction: row;

          p{
            font-size: 1.6rem;
          }
        }
      }

      .information__wrapper.--infor02{
        padding-bottom: 10.2rem;

        strong{
          padding-bottom: 0rem;
        }

        .information-list{
          li{
            padding-top: 2.2rem;
          }
        }
      }

      .information__wrapper.--infor03{
        strong{
          padding-bottom: 2.2rem;
        }

        .information-list{

          li{
            flex-direction: column;
            // padding-bottom: 3.2rem;
            .cate-product{
              margin-top: 0;
              width: 10rem;
              height: 3.2rem;

              font-size: 1.2rem;
              padding: 0.7rem 1.5rem;

            }

            ul{
              padding-bottom: 3.2rem;
            }

            p{
              padding-top: 0;
              padding-bottom: 9px;
            }
          }
        }
      }

    }
  }
}