// 🏠️ OSP-5111
@import '../../modules';

.main-osp5111{
  .--mobile{
    display: none;
  }

  strong{
    color: $azure;
  }

  // 뒷 배경
  .background-01{
    @include style_background-01(987px, -372px, -397px, 947px, -151px, -555px);
  }

  .tmaxos-products{
    padding: 9rem 0;
    text-align: center;
    background-color: #F8F9FA;

    h3{
      font-weight: 700;
      font-size: 36px;
      line-height: 1.5em;
    }

    .products-list__container{
      padding: 9rem 0;

      ul{
        display: flex;
        justify-content: space-between;
        gap: 4rem;

        li .btn-product{
          transition: transform 0.3s;

          min-width: 45rem; // 31%
          height: 36.2rem;
          padding: 5.1rem 3.6rem 3.6rem 3.6rem;
          background-color: #FFFFFF;
          box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.05);
          border-radius: 3rem;

          // 다운로드 버튼
          .btn-rounded-03{
            height: 4.7rem;
            width: 13.1rem;
            padding: 1.4rem 2.6rem;
            font-size: 1.8rem;
            font-weight: 400;
          }

          // 제품 이름
          .product-name{
            width: 100%;
            text-align: left;
            @include style_font(700, 3rem, 1.8em, black);
          }

          // 제품 설명
          .product-cont{
            margin-top: 3.2rem;
            // padding-bottom: calc(2.8rem + 1em);
            font-size: 18px;
            text-align: left;
            height: 14.2rem;

            color: $font_color-2C2C2C_black;
            line-height: 1.8em;
          }
          

          // 제품 관련 버튼들
          .product-more{
            display: flex;
            // height: 5rem;
            justify-content: space-between;
            align-items: center;
          }
        }

        // 동작정의
        .btn-product:hover{
          transform: translateY(-3rem);
          
          .btn-more .icon{
            transition: transform 0.6s;

            @include animation(0s, 0.8s, moveSideToSide);
            animation-iteration-count: infinite;
          }
        }

        .btn-product:active{
          background-color: rgba($color: white, $alpha: 0.9);
          box-shadow: none;

        }
      }
    }
  }


  // 각 제품 특징들 
  .product{

    .product__container{
      transition: all 0.6s;

      display: flex;
      justify-content: space-between;
      align-items: center;

      opacity: 0;
    }

    // 제품 이미지
    .img{ 
      width: 60rem;
      height: 60rem;
      margin: 6rem 0;
      background-size: cover;

      // imgs
      &.img_feature-1{background-image: url($img_path_to_pages + 'img_product/image-_tmaxgooroom.png');}
      &.img_feature-2{background-image: url($img_path_to_pages + 'img_product/image-_tmaxgooroom_sd.png');}
      &.img_feature-3{background-image: url($img_path_to_pages + 'img_product/image-superrds.png');}
    }

    // 제품 설명
    .feature-content{
      width: 56rem;

      h4{
        font-weight: 700;
        font-size: 36px;
        line-height: 1.6em;

        opacity: 0;
        transform: translateY(10%);
        transition: all 0.6s ease-in;
      }

      strong{
        display: block;
        padding-bottom: 2.2rem;
      }

      p{
        font-size: 2rem;
        line-height: 1.8em;
  
        opacity: 0;
        transform: translateY(10%);
        transition: all 0.6s ease-in;

      }
    }
  }

  // 사진이 왼쪽(하얀색 배경)
  .product.white{ 
    .img{
      transform: translateX(-5%);
      transition: transform 0.6s ease-out;
    }

    .feature-content{
      margin-right: 4rem;
    }
  }

  // 사진이 오른쪽(회색 배경)
  .product.gray{ 
    background-color: $bg_gray_f;
    
    .img{
      transform: translateX(5%);
      transition: transform 0.6s ease-out;
    }

    .feature-content{
      margin-left: 4rem;
    }
  }

  // * 🦄️Interaction
  .product.--visible{
    .product__container{
      opacity: 1;

      .img{
        transform: translateX(0);
      }

      .feature-content{

        h4{
          transform: translateY(0);
          opacity: 1;
        }

        p{
          transform: translateY(0);
          transition-delay: 0.6s;
          opacity: 1;
        }
      }
    }
  }
}





// * 👽️responsive
@include mobile-max{
  .main-osp5111{
    .--pcview{
      display: none;
    }
    .--mobile{
      display: block;
    }
    .background-01{
      @include style_background-01(488px, -201px, -244px, 0, -0, -0);
    }

    .tmaxos-products{
      padding-top: 4rem;
      padding-bottom: 6rem;

      h3{
        font-size: 1.8rem;
      }

      .products-list__container{
        padding: 4rem 3rem 0 3rem;
        overflow-x: scroll;

        ul{
          li .btn-product{
            padding: 3.8rem 2.0rem 3rem 2rem;
            
            // min-width: 23.6rem;  자꾸 줄내림 돼서 걍 바꿔버림..
            min-width: 23.8rem;
            height: 21.7rem;

            border-radius: 1rem;

            .btn-rounded-03{
              display: none;
            }

            .product-name{
              font-size: 1.6rem;
            }

            .product-cont{
              width: 100%;
              font-size: 1.2rem;
              margin-top: 2rem;
              height: 6.6rem;
              color: #848484;
            }

            .product-more{
              margin-top: 1rem;
            }
          }
        }
      }
    }

    .product{
      .product__container{
        padding: 0 3rem;
        flex-direction: column;
        padding-bottom: 6rem;

        .img{
          width: 30rem;
          height: 30rem;
          margin: 0;
        }

        .feature-content{
          width: 100%;
          margin: 0;
          text-align: center;

          h4{
            font-size: 2rem;
          }

          p{
            font-size: 1.3rem;
          }
        }
      }
    }

    .product.gray{ 
      .product__container{
        flex-direction: column-reverse;
        padding-bottom: 8.3rem;

      }
    }
  }
}