@import '../modules';

.custom{

  section{
    text-align: center;
    h2, h3{
      @include style_font(700, 3.6rem, 1, #1f1f1f);
      padding-bottom: 7.4rem;
    }
  }

  .pagination{
    margin-top: 4.8rem;
  }

  // * asset
  span.img-no-result{
    position: absolute;
    bottom: 0;
    left: -7.1rem;
    display: block;

    width: 20.4rem; 
    height: 29.8rem;
  }

  // 검색 결과 없을 때, 문의하기 버튼 Btn-no-result
  button.btn-no-result{
    width: 10.9rem;
    height: 3.4rem;
    padding: 0;
    margin-left: 1.7rem;
    margin-right: 1.9rem;
    font-size: 1.4rem;
    column-gap: 4px;

    // 연필 아이콘
    i.icon{
      width: 1.2rem;
      height: 1.2rem;
      background-image: none;
      margin-left: 0;
    }
  }

  // * 공지사항 + 묻고답하기   
  // 공지사항 & 묻고 답하기 공통
  .board-wrap{
    background-color: $white;

    .cont-ul{
      li{
        border-bottom: 1px solid #ececec;
        text-align: center;
        
        .cont{ 
          @include style_font(400, 2rem, 1, #212121);
          // display: inline-block;
          position: relative;
          padding: 3rem 0;
          margin-left: 10rem;
          text-align: left;

          .icon-table_new{
            margin-left: 6px;
          }

          // 게시물이 공지사항 일 때, 
          &.notice::before{
            position: absolute;
            content: '';
            top: 1.2em;
            left: -5rem;
            width: 2.8rem;
            height: 2.8rem;
            vertical-align: middle;
            background-image: url('../../../public/asset/icon/icon_table_notice.svg');
          }
        }

        .date{
          @include style_font(400, 1.8rem, 1, #797979);
        }
      }
    }
  }

  // 공지사항
  .board-wrap.notice{
    
    .title{
      display: grid;
      grid-template-columns: 10% auto 12%;
      column-gap: 8px;
    }

    .cont-ul{
      li{
        display: grid;
        grid-template-columns: 10% auto 12%;
        align-items: center;
        column-gap: 8px;
        height: 9.5rem;
        
        .--col1{
          justify-self: center;

          .cate{
            @include style_font(400, 1.4rem, 1, black);
            padding: 1.1rem 2.2rem;
            background-color: #eaeaea;
            border-radius: 3rem;
            cursor: default;

            &.--pr{
              background-color: #DCE1FF;
            }
          }
        }

        .date{
          justify-self: center;
        }
      }
    }
  }

  // 묻고 답하기 
  .board-wrap.question{
    .cont-ul{
      // 레이아웃
      li{
        display: grid;
        grid-template-columns: 12% 86%;
        grid-template-areas: 
          'tit tit'
          'cnt cnt';
        align-items: center;
        column-gap: 8px;
        position: relative;
        overflow: hidden;
      }

      .--col1{
        padding-left: 2.6rem;
        width: max-content;
        justify-items: self-start;
      
        .cate{
          font-size: 1.4rem;
          padding: 1.1rem 2.2rem;
          background-color: #eaeaea;
          border-radius: 3rem;
          background-color: #CFE9FD;
          cursor: default;

          &.--grsd{
            background-color: #CCF7F7;
          }
          &.--srds{
            background-color: #D3FFDF;
          }
          &.--etc{
            background-color: #EAEAEA;
          }
        }
      }

      .cont{
        display: inline-flex;
        justify-content: space-between;
        align-items: center;
        height: 9.6rem;
      }

      // 토글 창 열렸을 때,
      .cont.active{
        .icon{
          transform: rotate(180deg);
        }
      }

      // (토글) 문의 사항 상세 내용  
      .content-depth2{
        display: block;
        border-bottom: none;

        .question-board{
          grid-area: cnt;
          text-align: left;
          padding: 4.4rem 4.8rem 3.2rem 4.8rem;
          background-color: $bg_gray_f;
          border-radius: 0 0 3rem 3rem;

          p{
            margin-bottom: 6.4rem;
            @include style_font(400, 2rem, 2em, #4b4b4b);
          }

          .no-res{
            span{
              @include style_font(400, 1.4rem, 1, $azure);
            }
            width: max-content;
            margin-left: auto;
            
            .btn{
              width: 10.9rem; 
              height: 3.4rem;
              font-size: 14px;
              margin-left: 1rem;

              .icon{
                width: 1.4rem;
                height: 1.6rem;
                margin-left: 5px;
              }
            }
          }
        }
      }
    }
  }
}

@include mobile-max{
  .custom{

    section{
      h2, h3{
        padding-bottom: 4rem;
        font-size: 1.6rem;
      }
    }

    .pagination{
      margin-top: 3rem;
    }

    .board-wrap.notice{

      .title{
        display: none;
      }

      .cont-ul{
        padding: 0;
        li{
          padding: 0 2rem;
          grid-template-columns: none;
          grid-template-areas:
            "cate date"
            "cont cont";
          
          height: 8.7rem;

          .--col1{
            width: 100%;
            text-align: left;
            grid-area: cate;
            padding-left: 0;
  
            .cate{
              display: inline-block;
              padding: 6px 10px;
              font-size: 1rem;
            }
          }

          .cont{
            width: 100%;
            font-size: 1.4rem;
            grid-area: cont;
            padding: 0%;
            margin-left: 2px;
            
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;

            .icon-table_new{
              width: 1.4rem;
              height: 1.4rem;
            }
          }

          .--col3{
            text-align: right;
          }
          .date{
            grid-area: date;
            font-size: 1.2rem;
          }
        }
      }
    }

    .board-wrap.question{
      .cont-ul{
        padding: 0;

        li{
          width: 100%;
          padding: 1.3rem 2rem;
          grid-template-columns: none;
          grid-template-areas:
            "cate"
            "cont";
        }

        .--col1{
          padding-left: 0;
          grid-area: cate;

          .cate{
            padding: 6px 10px;
            font-size: 1rem;
          }
        }

        .cont{
          width: 100%;
          height: max-content;
          grid-area: cont;
          padding: 1.5rem 0 1rem 0;
          margin-left: 2px;
          font-size: 1.4rem;

          span{
            width: calc(100% - 1.6rem);
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
          }

          .icon-table_f_ar_d{
            width: 1.6rem;
            height: 1.6rem;
          }
        }

        .content-depth2{
          padding: 0;

          .question-board{
            padding: 1.6rem 2.2rem;
            border-radius: 0 0 1.5rem 1.5rem;
            
            p{
              margin-bottom: 2.4rem;
              font-size: 1.4rem;
            }

            .no-res{
              span{
                font-size: 1rem;
              }

              .btn{
                width: 8rem;
                height: 2.4rem;
                font-size: 1rem;
              }
            }
          }
        }
      }
    }

  }
}