@import '../../modules';

// 메인화면 배너 
.banner-slides{
  position: relative;
  height: 89.6rem;

  overflow-x: hidden;
  display: flex;
  
  // width: max-content;
  // width: 100vw;
  
  // 슬라이더
  .slider{
    position: relative;

    // width: 100%;
    // min-width: 100vw;
    min-width: 100%;

    height: 100%;

    background-position: left;
    background-size: cover;
    background-attachment: fixed;
    background-repeat: no-repeat;
    
    transform: translateX(0);

    // 슬라이드_내용
    .slider__content{
      position: absolute;
      top: 0;
      opacity: 0;
      width: 100%;

      @include animation(0.6s, 0.3s, fadeIn);

      .content__container{
        width: 1440px;
        margin: 0 auto;
        height: 100%;
        padding-left: 5.4rem;
        color: $white;

        h1{
          @include style_font(700, 5.2rem, 1.4em, $white);
          
          width: 52.7rem;
          
          padding-top: calc(19.2rem + 8.4rem);
          margin-bottom: 1.6rem;
        }
  
        p{
          width: 67.8rem;
  
          line-height: 1.8em;
          padding-top: 0.4rem;
          padding-bottom: 5.2rem;
        }
      }

    }
  }
  
  // 슬라이드 컨트롤러 
  .slider__controller{
    position: absolute;
    width: 100vw;
    bottom: 28rem;

    .controller__wrapper{
      // background-color: red;
      padding-left: 5.4rem;

      .controller__container{
        // background-color: rgba(0, 0, 0, 0.08);
        display: flex;
        justify-content: space-between;
        align-items: center;

        width: 13rem;

        .container__pagination{
          text-align: center;
          width: 6.1rem;
          color: rgba(255, 255, 255, 0.4);

          // now number
          .page-now{
            color: rgba(255, 255, 255, 1);
            font-weight: 700;
          }

          .icon_banner-slash{
            margin: 0 7px;
          }

        }
  
        button{
          .icon{
            width: 3rem;
            height: 3rem;
            
          }
        }

        button.--limit{
          user-select: none;
          pointer-events: none;
          cursor: initial;
          
          .icon{
            // fill-opacity="0.4"
            opacity: 0.4;
          }
        }
      }
      
      // * 반응형 시 
      .controller__bar{
        display: none;
      }
    }
  }


  // 슬라이드 배경
  .slider.slide-01{
    background-image: url($img_path_to_pages + 'main_banner.png');
  }

  // .slider.slide-02{
  //   background-color: gainsboro;
  // }

  // .slider.slide-03{
  //   background-color: black;
  // }

  // .slider.slide-04{
  //   background-color: red;
  // }
  
  // .slider.slide-05{
  //   background-color: whitesmoke;
  // }
}


.main-osp3111{
  position: relative;
  background-color: $bg_color-F8F8F8-grey;

  h3{
    @include style_font(700, 3.6rem, 1.4, black);

    span{
      display: block;

    }
    strong{
      color: $azure;
    }
  }

  .main-notice{
    position: absolute;
    top: -73px;
    width: 100%;
    z-index: 9;

    .notice__container{
      display: flex;
      align-items: center;
      height: 73px;
      justify-content: space-between;

      padding: 0 2rem;
      background-color: rgba(0, 10, 47, 0.44);
      border-radius: 1em 1em 0 0;

      span{
        color: $white;
        padding: 0 2rem;
        font-weight: 700;
      }

      a{
        color: $white;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }

      .btn-notice-now{
        width: calc(100% - 258px);
        padding: 1em;
        font-weight: 300;
      }

      .btn-notice-all{
        width: max-content;
        text-align: right;
        font-weight: 500;

        .icon-more_arrow{
          margin-left: 4px;
          background-image: none;
        }
      }
    }
  }

  // 메인 제품
  .main-products{
    position: relative;
    text-align: center;

    // background-circle
    background: linear-gradient(90.72deg, #4661FF 0.62%, #279FFF 99.38%);
    z-index: 0;
    overflow: hidden;
    &::before{
      position: absolute;
      content: '';
      z-index: -1;
      background-repeat: no-repeat;
      // background-image: url($img_path_to_pages + 'res/bg_circle_1.svg');
      background-size: 2020px;
      width: 2020px;
      height: 2020px;
      top: -868px;
      left: -686px;
      opacity: 0.1;
    }
    &::after{
      position: absolute;
      content: '';
      z-index: -1;
      background-repeat: no-repeat;
      // background-image: url($img_path_to_pages + 'res/bg_circle_1.svg');
      // mix-blend-mode: multiply;
      background-size: 1693px;
      width: 1693px;
      height: 1693px;
      top: 261px;
      right: -841px;
      opacity: 0.08;
    }

    .product__container{
      padding: 9.6rem 0;

      h3{
        color: $white;
        padding-bottom: 9.6rem;
      }
      
      .product__btns{
        display: flex;
        justify-content: space-between;

        flex-wrap: nowrap;

        .btn-product{
          width: 462px;
          min-width: 46.2rem;
          height: 645px;
          padding: 6px 4rem 57px 4rem;

          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: space-between;
  
          background-color: rgba($color: white, $alpha: 1);
  
          box-shadow: 0px 20px 20px rgba(0, 0, 0, 0.08);
          border-radius: 3rem;
          
          // 이미지
          .img{
            display: block;
            width: 34rem;
            height: 34rem;
  
            &.img_feature-1{background-image: url($img_path_to_pages + 'img_product/image-_tmaxgooroom.png');}
            &.img_feature-2{background-image: url($img_path_to_pages + 'img_product/image-_tmaxgooroom_sd.png');}
            &.img_feature-3{background-image: url($img_path_to_pages + 'img_product/image-superrds.png');}
          }
  
          // 제품명
          h4{
            font-size: 32px;
            font-weight: 700;
            margin: 34px 0 36px 0;
          }
  
          // 제품 내용
          p{
            display: block;
            height: 81px;
            font-size: $font_size-basic;
            line-height: 150%;
            color: $font_color-848484_grey;
          }
          // 자세히 보기 
          .btn-more{
            .icon{
              margin-left: 4px;
            }
          }
        }
  
        // 동작 정의 
        .btn-product{
          // hover 애니메이션
          transition: transform 0.3s;
          
          .img{
            transition: transform 0.3s;
            transform: scale(1.018);
          }
  
          &:hover{
            transform: translateY(-4rem);
  
            // 이미지 커짐
            .img{
              transform: scale(1.0625);
            }
  
            // 화살표 움직임
            .btn-more .icon{
              transition: transform 0.6s;
  
              @include animation(0s, 0.8s, moveSideToSide);
              animation-iteration-count: infinite;
            }
          }
  
          // press 애니메이션
          &:active{
            box-shadow: none;
            background-color: rgba($white, $alpha: 0.9);
          }
        }
      }
    }
  }

  .main-board{
    background-color: $white;
  }
}


// * 👽️responsive
@include mobile-max{

  .banner-slides{
    height: 52.6rem;
    .slider{

      .slider__content{
        width: 100%;
        padding: 0 2rem;

        .content__container{
          width: 100%;
          padding-left: 0;

          h1{
            padding-top: 14.6rem;
            font-size: 2.4rem;
            width: 100%;
          }
          p{
            width: 100%;
          }
        }
      }
    }

    .slider__controller{
      bottom: 5.8rem;
      .controller__wrapper{
        padding-left: 0;
        .controller__container{
          padding: 0 1rem;
          width: 100%;
        }

        .controller__bar{
          position: relative;
          margin-top: 1.2rem;

          display: block;
          width: 100%;
          height: 2px;

          background-color: rgba(0, 15, 90, 0.5);

          .bar{
            position: absolute;
            left: 0;
            height: 2px;
            width: 20%;

            background-color: $white;
          }
        }
      }
    }
  }



  // 페이지
  .main-osp3111{
    h3{
      font-size: 1.6rem;
      line-height: 1.5;
    }
    // 공지사항
    .main-notice{
      top: -58px;
      .notice__container{

        height: 5.8rem;
        border-radius: 0;
        span{
          padding: 0;
        }

        .btn-notice-now{
          width: 80%;
        }

        .btn-notice-all{
          span{
            display: none;
          }
        }
      }

    }

    // 메인 제품
    .main-products{
      transition: all 0.3s;

      .product__container{
        padding: 4.4rem 0 5rem 0;

        h3{
          padding-bottom: 0rem;
        }

        .product__btns{
          overflow-x: scroll;
          overflow-y: hidden;   
          white-space: nowrap;
          padding-top: 4.4rem;

          gap: 1.2rem;

          padding: 4.4rem 2rem 0 2rem;

          .btn-product{
            min-width: 235px;
            height: 34.2rem;
            padding: 6px 2rem 3.2rem 2rem;
            border-radius: 1rem;

            .img{
              width: 16rem;
              height: 16rem;
            }

            h4{
              font-size: 1.6rem;
              margin: 0 0 1.8rem 0;

            }

            p{
              width: 100%;
              font-size: 1.2rem;
              line-height: 1.8;
              word-break: break-all;
              white-space: pre-wrap;
            }

            .btn-more{  
              font-size: 1rem;
            }
          }
        }
      }
    }
  }
}
