@import './../modules';

footer{
  background-color: $bg_gray_f;
  color: $font_color-737373_grey;

  // 사이트-맵
  .footer__sitemap{
    border-bottom: 1px solid $line_color-E7E7E7_grey;
    
    // depth1
    .sitemap-depth1{
      display: flex;
      padding: 5.6rem 0;

      li{
        font-size: 14px;
        width: 25rem;

        h4{
          position: relative;
          padding-bottom: 2rem;
          display: flex;
          justify-content: space-between;
          font-weight: 700;
          line-height: 1.5;

          .icon-wrap{
            display: none;
          }

          .icon{
            position: relative;
            background-image: none;
            width: 1.4rem;
            height: 1.4rem;
          }
        }
      }

      // 나머지 사이트
      .another-site{
        width: 10rem;
        @include style_font(700, 1.4rem, 1, #737373);
        position: relative;

        display: flex;
        flex-direction: column;
        justify-content: space-between;


        h4 .icon{
          display: inline-block;
          position: relative;
          margin-left: 2px;

          .svg{
            width: 18px;
            height: 18px;
          }
        }
        
        // ButtonLinkHR
        .btn-lnb-depth1{
          width: 100%;
          // height: 100%;
          display: inline-flex;
          align-items: center;
          justify-content: flex-start;
    
          .icon{
            width: 1.6rem;
            height: 1.6rem;

            .svg path{
              fill: #737373;
            }
          }
        }

        .depth2-toggle.--links{
          position: relative;
          display: flex;
          padding-bottom: 0;
        }

        .sitemap-depth2{
          position: absolute;
          top: 100%;
          width: 100%;
          padding-left: 1rem;

          background-color: white;
          z-index: 9;
        }
      }
    }

    // depth2
    .sitemap-depth2{
      li a{
        display: inline-block;
        padding: 1rem 0;
      }
    } 
  }


  // 사이트-정보
  .footer__information{
    justify-content: space-between;
    padding: 4rem 0;
    
    .logo-wrapper{
      display: flex;
      margin-bottom: 1.9rem;

      .logo{
        width: 12rem;
        height: 2.8rem;
        margin-right: 12rem;
      }

      .information-rules{
        // margin-bottom: 1.6em;
  
        li{
          display: inline-block;
          font-weight: 700;
          margin-right: 3.2em;
          line-height: 1.5;
        }
      }
    }

    // 회사 정보 및 방침
    .information{
      position: relative;
      margin-left: 24rem;
      // display: flex;
      // width: 75%;
      font-size: 1.4rem;
      // padding-top: 1.9rem;
      address, small{
        font-size: inherit;
        padding-bottom: 1.2rem;
        line-height: 1.5;
      }
      
      // 인증마크 확대 버튼
      .btn-certificate{
        position: absolute;
        top: 0;
        right: 0;
        // align-self: flex-end;
        width: 9rem;
        height: 6rem;
  
        img{
          width: 100%;
          height: 100%;
        }
      }
    }
  }
}





// * 👽️responsive
@include mobile-max{
  footer{

    // 사이트 맵
    .footer__sitemap{
      border-bottom: 0;

      .sitemap-depth1{
        flex-wrap: wrap;
        padding: 3.4rem 2rem 0 2rem;
        row-gap: 1rem;

        li{
          font-size: 1.3rem;
          padding: 0 1rem;
          min-width: 100%;

          h4{
            font-weight: 500;
            padding-bottom: 0;
            cursor: pointer;

            .icon-wrap{
              display: block;
            }
            .icon{
              display: inline-block;
            }
          }
        }

        .sitemap-depth2{
          padding-top: 1.2rem;
          padding-bottom: 1rem;
          // display: none;
          // height: 0;
          // overflow: hidden;
        }

        .another-site{
          border-bottom: 1px solid $line_color-E7E7E7_grey;
          border-top: 1px solid $line_color-E7E7E7_grey;

          margin-top: 1rem;
          padding-top: 2rem;
          padding-bottom: 2rem;
          row-gap: 1rem;

          a{
            font-weight: 500;
            font-size: 1.3rem;
          }

          .link-to-another{

            .icon{
              position: absolute;
              right: 0;
              width: 1.4rem;
              height: 1.4rem;
              svg{
                width: 1.4rem;
                height: 1.4rem;
              }
            }
          }

        }
      }

      .sitemap-depth2{
        display: none;
      }
      .sitemap-depth2.--visible{
        display: block;
      }
    }

    // 사이트 정보
    .footer__information{
      flex-direction: column;
      padding: 2rem 3rem 3.4rem 3rem;


      .logo-wrapper{

        .logo{
          width: 8rem;  
          height: 1.86rem;
          margin-right: 4.6rem;

        }

        .information-rules{
          li{
            font-weight: 500;
          }
        }
      }

      // 회사 정보 및 방침
      .information{
        margin-left: 0rem;

        address, small{
          padding-bottom: 0;
          font-size: 1.1rem;
        }
      }

      .btn-certificate{
        display: none;
      }
    }
  }
}