@import '../../modules';


.main-osp4112{
  padding: 14rem 0 18.7rem 0;

  .width1352{
    width: 1352px;
    margin: 18rem auto;
  }

  .greeting__container{
    display: flex;
    padding: 5.8rem 7.5rem 13rem 8.7rem;
    box-shadow: 0px 30px 50px rgba(175, 175, 175, 0.1);
    border-radius: 50px;

    .greeting-ceo{
      position: relative;

      // 사진
      .img_tamxos_ceo{
        display: block;
        width: calc(428px + 5.5rem);
        height: 467px;
        background: url($img_path_to_pages + 'img-OSP4112-ceo.png') right no-repeat;
        background-size: contain;
      }
      
      .ceo-title{
        position: absolute;
        top: 11.3rem;

        h3{
          font-weight: 700;
          font-size: 36px;
          padding-bottom: 2rem;
          
          // 파란 점 
          position: relative;
          &::before{
            position: absolute;
            content: '';
            top: -4.9rem;
            left: -2.4rem;
            width: 3rem;
            height: 3rem;
            background-size: cover;
            background-image: url('../../../../public/asset/icon/icon_subtract.svg');
          }
        }
        
        h4{
          font-weight: 700;
          font-size: 18px;
        }

      }
    }

    .greeting-content{ 
      position: relative;
      margin-top: 2.5rem;
      margin-left: 8.4rem;

      // icon quote
      &::after,
      &::before{
        position: absolute;
        content: '';
        width: 142px;
        height: 105px;

        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        background-image: url('../../../../public/asset/icon/icon_quotes.svg');
      }
      // 아래쪽
      &::after{
        bottom: -2rem;
        right: -4rem;
        transform: rotate(180deg);
      }
      // 위쪽
      &::before{
        top: -7rem;
        left: -12rem;
        mix-blend-mode: multiply;
      }

      p{
        @include style_font(300, 1.8rem, 1.8, #212121);
        padding-bottom: 1.6em;
      }

    }
  }

}




// * 👽️responsive
@include mobile-max{
  .main-osp4112{
    
    .width1352{
      width: 100%;
      margin: 0;
    }

    .greeting__container{
      padding: 4.7rem 4rem 10.7rem 4rem;
      flex-direction: column;

      .greeting-ceo{

        .img_tamxos_ceo{
          width: 16.3rem;
          height: 17.8rem;
          margin-left: auto;
        }
        
        .ceo-title{
          top: 5.3rem;
          
          h3{
            font-size: 1.8rem;
            padding-bottom: 9px;

            &::before{
              top: -2.3rem;
              left: -1.2rem;
              width: 1.5rem;
              height: 1.5rem;
              // transform: scale(0.5);
              // transform-origin: bottom;
            }
          }
  
          h4{
            font-size: 1.2rem;
          }
        }
      }

      .greeting-content{
        margin: 0;
        margin-top: 9rem;

        // icon quote
        &::after,
        &::before{
          position: absolute;
          content: '';
          width: 5rem;
          height: 5rem;
        }
        &::after{
          right: 0;
          bottom: 0;

        }
        &::before{
          top: -5rem;
          left: 0;
        }

        p{
          font-size: 1.2rem;
        }
      }
    }
  }
}