@import '../../modules';

// 공통
.custom{
  .search-table{
    margin-top: 7rem;
    margin-bottom: 2.4rem;
  }

  // 검색 결과 없을 때,
  div.no-result{
    position: relative;
    padding: 11.5rem 0;
    margin-bottom: 10.6rem;
    border-radius: 2rem;
    background-color: $bg_gray_f;
    
    .result-key{
      @include style_font(700, 2.8rem, 1, $azure);
      padding-bottom: 2rem;
    }
    span{
      @include style_font(500, 2.4rem, 1, $font_color-212121_grey);
    }
    p{
      margin-top: 6.8rem;
      @include style_font(500, 1.8rem, 2em, $font_color-797979_grey);
    }

    button span{
      text-align: center;
      color: white;
      font-size: 1.4rem;
    }
  }
}

.main-osp7111{

  // 배경 
  .background-01{
    @include style_background-01(863px, -442px, -460px, 1298px, -175px, -770px);
    padding-top: 9.6rem;
    padding-bottom: 20.5rem;  
  }

  // 궁금한 점이 있으신가요? 
  .products{
    h2{
      padding-bottom: 9.6rem;
    }

    .menu-list{ 
      display: flex;
      gap: 3.9rem;
      justify-content: center;
      z-index: 9;
      transition: background-color 0.2s;

      .btn-link-customer{
        display: flex;
        flex-direction: column;
        width: 45.4rem;
        padding: 4rem 4.7rem;
        text-align: left;
        background-color: rgba($white, $alpha: 1.0);
        box-shadow: 0rem 1rem 4rem rgba(0, 0, 0, 0.05);
        border-radius: 3rem;

        .icon{
          width: 10rem;
          height: 10rem;
          background-position-x: -1rem;
          // icon img
          &.icon-custom-01{background-image: url($img_path_to_icons + 'icon_CS-01.svg');}
          &.icon-custom-02{background-image: url($img_path_to_icons + 'icon_CS-02.svg');}
          &.icon-custom-03{background-image: url($img_path_to_icons + 'icon_CS-03.svg');}
        }

        h6{
          padding-top: 1.5rem;
          padding-bottom: 1.8rem;
          @include style_font(700, 2.8rem, 1.4em, #1f1f1f);
        }

        span{
          @include style_font(400, 2rem, 1.6em, #848484);
          // width: 28rem;
        }
      }

      // 버튼 동작 정의 
      .btn-link-customer{
        transition: transform 0.3s;

        &:hover{
          transform: translateY(-3rem);
        }
        &:active{
          box-shadow: 0rem 1rem 4rem rgba(0, 0, 0, 0.00);
          background-color: rgba($color: white, $alpha: 0.9);
        }
      }
    }
  }

  // 도움이 필요한 제품을 선택하세요.
  .custom-products{
    padding-top: 9.8rem;
    padding-bottom: 11.2rem;
    // 타이틀
    h3{
      padding-bottom: 9.8rem;
    }

    .products-list{
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 3.9rem;

      .btn-product{
        @include style_font(700, 3.6rem, 1, white);
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 45.4rem;
        height: 17.2rem;
        padding: 0 4rem;
        text-align: left;
        background: linear-gradient(270deg, #44BCFF 0%, #4BA5FF 100%);
        border-radius: 3rem;
      }

      .icon_costom-ar-r{
        width: 4.6rem;
        height: 4.6rem;
      }

      // 버튼 동작 정의 
      button{
        transition: all 0.3s;
        &:hover{
          padding-right: 3rem;
          transform: translateY(-2rem);
        }
        &:active{
          opacity: 0.9;
        }
      }
    }
  }

  .board-wrap{
    box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.05);
    border-radius: 3rem;
    padding-bottom: 4.8rem;

    .cont-ul{
      padding: 0 4.4rem;
      margin-bottom: 4.8rem;

      div.--col1{
        padding-left: 2.6rem;
        justify-self: self-start;
      }
    }
  }

  .custom-notice{
    padding-top: 9.6rem;
    padding-bottom: 7.6rem;
    background-color: $bg_gray_f;

    .board-wrap.notice{
      padding-top: 1.6rem;
    }
  }

  .custom-question{
    padding-top: 9.6rem;
    padding-bottom: 5.6rem;
    background-color: $bg_gray_f;

    div.board-wrap.question ul.cont-ul{
      padding-top: 3.6rem;
    }
  }
}


// * 👽️responsive
@include mobile-max{
  .main-osp7111{

    .products{
      padding-top: 4rem;
      padding: 4rem 1.6rem;

      h2{
        font-size: 1.6rem;
        padding-bottom: 4rem;
      }

      .menu-list{
        width: 100%;
        flex-direction: column;
        padding: 1.8rem 0;
        background-color: rgba($white, $alpha: 1.0);
        border-radius: 1.5rem;

        .btn-link-customer{
          width: 100%;
          padding: 1.5rem 1rem;
          box-shadow: none;
          background-color: rgba($white, $alpha: 0.1);

          .icon{
            position: absolute;
            width: 5rem;
            height: 5rem;
            background-position: center;
          }

          h6{
            padding: 0;
            padding-left: 7rem;
            font-size: 1.4rem;
          }

          span{
            font-size: 1.2rem;
            padding-left: 7rem;

          }
        }
      }
    }

    .custom-products{
      padding: 4rem 1.6rem 3rem 1.6rem;

      h3{
        font-size: 1.6rem;
        padding-bottom: 4rem;
      }

      .products-list{
        flex-direction: column;
        row-gap: 8px;
        width: 100%;


        li{
          width: 100%;
        }

        .btn-product{
          width: 100%;
          height: 7.2rem;
          border-radius: 1.5rem;
          font-size: 1.6rem;
        }

        .icon_costom-ar-r{
          width: 2rem;
          height: 2rem;
        }
      }
    }

    .board-wrap{
      padding-bottom: 2.4rem;

      .cont-ul{
        margin-bottom: 2.4rem;
      }
    }

    .custom-notice{
      padding-top: 4rem;
      padding-bottom: 4rem;
    }

    .custom-question{
      padding-top: 0rem;

    }
  }
}