@import './../modules';

// subpage에 banner - basic
.subpage-banner{
  position: relative;
  padding-top: $number_nav_height-84;
  height: calc(38rem + 8.4rem);

  color: $white;
  background: linear-gradient(90.72deg, #4661FF 0.62%, #4BA5FF 99.38%);

  // 서브 메뉴
  .subpage-depth2{
    position: absolute;
    width: 100%;
    display: block;
    background-color: rgba(52, 92, 224, 0.18);
    z-index: 9;

    // depth2 메뉴들
    .depth2__container{
      display: flex;

      a{
        display: inline-block;
        padding: 2.2rem 0.5rem 2.4rem 0.5rem;
        margin: 0 1em;

        font-weight: 500;
        color: rgba(255, 255, 255, 0.6);
        @include grow_fontw(white, 0.3s);
      }
    }

  }

  // 서브 내용
  .subpage-cont{
    position: relative;
    padding: 16rem 1rem 4.6rem 1rem;
    vertical-align: baseline;

    div{
      position: absolute;
      bottom: 0%;
    }

    h2{
      padding-bottom: 3.2rem;
      @include style_font(700, 3.8rem, 1, white);
      transform: translateX(3rem);

    }

    p{
      height: 10rem;
      transform: translateX(3rem);

      @include style_font(400, 2.4rem, 1.8em, white);
      span{display: block;}
    }

    // tmax구름의 경우 다운로드 버튼이 있음
    .btn-download-tmaxgr{
      padding: 1.6rem 6.5rem ;
      position: absolute;
      bottom: 5.6rem;
      right: 20.4rem;

      width: 23rem;
      height: 6.8rem;

      &:hover{
        background-color: $white;
      }
    }
  }
}

.subpage-banner.commu{ 
  background: linear-gradient(90.72deg, #00BAE2 0.62%, #00E0D3 99.38%);
  
  .subpage-depth2 .depth2__container{
    // ! 임시 
    :not(li:first-child) a{
      display: none;
    }
  }
}


// * 배경 이미지 (배너 이미지)
.subpage-banner{

  .illust-subpage{
    position: absolute;
    bottom: 0;
    right: 0;  
    width: 829px;
    height: 380px;
    background-size: cover;
  }

  .img_banner-subpage-1{ // TmaxOS소개
    background-image: url('../../../public/asset/images/banner/img_2depth_introduce.png');
  }
  .img_banner-subpage-2{
    background-image: url('../../../public/asset/images/banner/img_2depth_product.png');
  }
  .img_banner-subpage-3{
    background-image: url('../../../public/asset/images/banner/img_2depth_promotion.png');
  }
  .img_banner-subpage-4{
    background-image: url('../../../public/asset/images/banner/img_2depth_customer.png');
  }
  .img_banner-subpage-5{
    background-image: url('../../../public/asset/images/banner/img_2depth_community.png');
  }
  .img_banner-subpage-6{ // 마이 페이지
    background-image: url('../../../public/asset/images/banner/img_2depth_mypage.png');
  }
}


// * 🦄️Interaction
.subpage-banner{

  // 안내문구
  .subpage-cont{
    h2{
      @include animation(0s, 1s, bannerContRightToLeft, ease-out);
    }
    p{
      @include animation(0s, 1s, bannerContRightToLeft, ease-out);

      // @include animation(0.1s, 1s, bannerContRightToLeft, ease-out);
    }
  }

  // 배너 이미지
  .illust-subpage{
    @include animation(0s, 2s, fadeInSeventToTem);
  }
}




// * 👽️responsive
@include mobile-max{
  
  .subpage-banner{
    padding-top: 4.8rem;
    height: calc(13.5rem + 9.6rem);

    .subpage-depth2{

      .depth2__container{
        a{
          font-weight: 400;
          padding: 1.7rem 0.5rem 1.9rem 0.5rem;
          font-size: 1.2rem;
        }
      }
    }

    .subpage-cont{
      padding: 8.7rem 1rem 0 2rem;

      h2{
        font-size: 1.8rem;
        padding-bottom: 1.8rem;
      }

      p{
        display: none;
        &.important{
          display: block;
          font-size: 1.2rem;
          line-height: 1.4;
        }
      }

      .btn-download-tmaxgr{
        display: none;
      }
    }

    .illust-subpage{
      width: 29.4rem;
      height: 13.5rem;
    }
  }
}