@import '../../modules';

.section-osp0112{
  background-color: $bg_gray_f;
  padding-top: 4rem;
  padding-bottom: 9.6rem;

  .products-tos-list{
    padding-top: 2.4rem;
    margin-bottom: 7.6rem;
  }
}

// tos 구름 버전 리스트 
.products-tos-list{

  li{
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 5.4rem 7rem 5rem 7rem;
    margin-bottom: 2.4rem;

    text-align: left;

    background-color: $white;
    box-shadow: 0px 5px 20px rgba(45, 52, 74, 0.03);
    border-radius: 2rem;

    // 제품 정보
    .tos-product{
      
      .title{
        @include style_font(900, 3.4rem, 1, $font_color-212121_grey);
      }
      
      .ver{
        @include style_font(500, 2.6rem, 1, #666);
        padding-left: 8px;
      }

      p{
        @include style_font(400, 1.6rem, 1, #91989D);
        position: relative;
        margin-top: 2.4rem;
        padding-left: 1rem;
        strong{
          font-weight: 700;
        }

        &::after{
          position: absolute;
          content: '';
          width: 5px;
          height: 5px;
          background-color: $commu_cyan_01;
          left: 0;
          top: 0.3em;
          border-radius: 50%;
        }
      }
    }

    // 다운로드 버튼들
    .btns-downloads{
      display: inline-flex;
      align-items: flex-end;
      column-gap: 2rem;

      .btn{
        height: 4.7rem;
      }
    }
  }

  li:last-child{
    margin-bottom: 0;
  }
}

@include mobile-max{
  .section-osp0112{
    padding-bottom: 4rem;

    .products-tos-list{
      padding: 2.4rem 1.6rem 0 1.6rem;
      margin-bottom: 3.4rem;

    }
  }

  .products-tos-list{

    li{
      position: relative;
      padding: 2.5rem;
      flex-direction: column;
      .tos-product{
        
        .title{
          font-size: 1.6rem;
        }
        
        .ver{
          font-size: 1.3rem;
        }
        
        p{
          margin-top: 1rem;
          font-size: 1.2rem;
        }
      }

      .btns-downloads{
        padding-top: 3rem;

        .--install{
          display: none;
        }

        .btn{
          height: 3.6rem;

          font-size: 1.4rem;
          padding: 0 1.6rem;
        }
      }
    }
  }
}