@import '../../modules';


// 비디오 컴포넌트 속성 (홍보 공통)
.video{

    .media-list{
        display: flex;
        justify-content: space-between;

        text-align: left;
        
        // 비디오 아이템
        li .item-card.--video{
            position: relative;
            box-shadow: 0px 15px 40px rgba(0, 0, 0, 0.1);
            border-radius: 10px;
            background-color: #fff;

            transition: transform 0.3s;
            overflow: hidden;

            .wrap-img{
                height: 230px;
                overflow: hidden;

                img{
                    width: 100%;
                    height: 100%;
                }
            }

            .content{
                position: relative;
                padding: 30px;

                .icon-play{
                    position: absolute;
                    width: 4.4rem;
                    height: 4.4rem;
                    left: 3rem;
                    top: 3.8rem;
                    background-color: transparent;
                }

                strong{
                    display: inline-block;
                    line-height: 1.5;
                    padding-left: 6rem;
                    font-size: 20px;
                }
            }

            // hover action시 나오는 파란 화면
            &::after{
                position: absolute;
                content: '';
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background-image: url('../../../../public/asset/icon/icon_play_white.svg');
                background-position: center;
                background-repeat: no-repeat;
                background-size: 13rem;
                
                background-color: rgba($azure, $alpha: 0.6);
                opacity: 0;
            }
        }
    }
}

// news 배열 (홍보 공통)
.news{
    .item-list{
        // 쌓일 때 특징 때문에 
        display: flex;

        justify-content: flex-start;
        column-gap: 6rem;
        flex-wrap: wrap;


        li{
            position: relative;

            width: 30.556%;
            min-width: 44rem;

            &:not(:nth-child(n+6)){
                margin-bottom: 126px;
            }

            .wrap-img{
                border-radius: 10px;
                overflow: hidden;
                height: 260px;

                img{
                    height: 100%;
                    object-fit: cover;
                }
            }

            strong{
                line-height: 1.5em;
                font-weight: 400;
                font-size: 20px;
                margin-top: 20px;
                display: inline-block;
                margin-bottom: 17px;
                color: #3c3c3c;
            }
            transform: translateY(-0);

            transition: transform 0.3s;
        }
        .ico-new{
            background-color: #2C99FF;
            border-radius: 50%;
            width: 21px;
            height: 21px;
            display: inline-block;
            // background-image: url('../../public/assets/images/ico-new.png');
            background-image: url($img_path_to_pages + 'ico-new.png');
            background-repeat: no-repeat;
            background-position: center;

            position: relative;
            top: 3px;
            margin-left: 6px;
        }

        li:hover{
            transform: translateY(-30px);

            div.wrap-img{
                box-shadow: 0px 20px 50px rgba(0, 0, 0, 0.1);
                transition: box-shadow 0.3s;
            }
        }
    }
    .pagination{
        margin: 0 auto;
        margin-bottom: 96px;
        margin-top: 76px;
    }
}


.main-osp6111{

    h3{
        @include style_font(700, 3.6rem, 1.5, black);
        
        span{
            display: block;
        }
    
        strong{
            color: $azure;
        }
    }

    .promotion-overview{
        position: relative;
        background: linear-gradient(270deg, #44BCFF 0%, #4BA5FF 100%);
        border-radius: 50px 50px 0 0;
        text-align: center;
        overflow: hidden;
    
        // background 원
        z-index: 0;
        &::after{
            z-index: -1;
            position: absolute;
            content: '';
            left: -346px;
            top: 341px;
            width: 992px;
            height: 992px;
            border-radius: 50%;
            background: linear-gradient(180deg, rgba(255, 255, 255, 0.15) 0%, rgba(255, 255, 255, 0) 100%);
        }
        
        .video{
            padding: 96px 0;
            
            .title{
                text-align: center;
                color: #fff;
                font-weight: bold;
                font-size: 36px;
            }
    
            .media-list{
                margin-top: 96px;
    
                li{
                    width: 30%;
                    
                    .item-card.--video{
                        display: flex;
                        flex-direction: column;
    
                        .content{
    
                            strong{
                                display: block;
                            }
    
                            .icon-play{
                                position: absolute;
                                right: 37px;
                                bottom: 32px;
    
                                display: inline-block;
                                width: 4rem;
                                height: 3.4rem;
    
                                background-size: 100% 100%;
                            }
                        }
                    }
                }
            }
        }
    
        // 더보기 버튼
        .btn-more{
            display: inline-block;
            color: white;
            margin-top: 10rem;
            line-height: 1em;
    
            .icon{
                background-image: none;
            }
        }
    }
}


// * 🦄️Interaction
.main-osp6111{
    .main-board{
        // 타이틀 fadein
        .board__container h3{
            transition: transform 0.6s;
            animation-name: moveUp, fadeIn;
            // animation-duration: 0.9s, 0.9s;
            animation-duration: 1s, 1s;
            animation-timing-function: ease;
        }

        // card가 순서대로 올라가는 애니메이션
        .board-cards{
            button{
                transition: all 0.3s;
                transform: translateY(4rem);
                opacity: 0;
            }
            button:nth-child(1){
                @include animation(0s, 1s, cardUp, cubic-bezier(0.25, 0, 0.40, 1));
                // @include animation(0s, 0.6s, cardUp, ease-in-out);
            }
            button:nth-child(2){
                @include animation(0.25s, 1s, cardUp, cubic-bezier(0.25, 0, 0.40, 1));
            }
            button:nth-child(3){
                @include animation(0.5s, 1s, cardUp, cubic-bezier(0.25, 0, 0.40, 1));
            }
            button:nth-child(4){
                @include animation(0.75s, 1s, cardUp, cubic-bezier(0.25, 0, 0.40, 1));
            }
        }
    }

    .video .media-list{
        li{
            opacity: 0;
        }
    }
}

.video .media-list{
    li {
        transform: translateY(3rem);
        transition: all 0.3s;
    }

    // video 콘텐츠 hover
    .item-card.--video:hover{
        transform: translateY(-3rem);
        // 파란화면
        &::after{
            opacity: 1;
            background-size: 10rem;
            transition: all 0.4s;
        }
    }

    // video 콘텐츠 active
    .item-card.--video:active{
        &::after{
            background-color: rgba($azure, $alpha: 0.8);
        }
    }
}

// scroll시 animation 클래스 추가,
// 컨텐츠가 올라가는 순서대로 올라가는 애니메이션
.--animation{
    .video .media-list{
        li:nth-child(1){
            @include animation(0s, 1s, cardUp, cubic-bezier(0.25, 0, 0.40, 1));
        }
        li:nth-child(2){
            @include animation(0.25s, 1s, cardUp, cubic-bezier(0.25, 0, 0.40, 1));
        }
        li:nth-child(3){
            @include animation(0.5s, 1s, cardUp, cubic-bezier(0.25, 0, 0.40, 1));
        }
    }
}



// * 👽️responsive
@include mobile-max{
    // news 공통
    .news{

        .item-list{
          padding: 0 2rem;
    
          li{
            width: 100%;
            min-width: 32rem;
            padding-bottom: 4rem;
            
            .wrap-img{
              height: 19rem;
            }
    
            &:not(:nth-child(n+6)){
              margin-bottom: 0;
            }
    
            strong{
              margin-top: 1rem;
              margin-bottom: 6px;
              font-size: 1.4rem;
            }
    
            .date{
              display: block;
              font-size: 1.2rem;
            }
          }
    
        }
    
        .pagination{
          margin-top: 0%;
          margin-bottom: 4rem;
        }
    }

    .video{

        .title{
            font-size: 1.6rem;
        }

        .media-list{
            li{
                .item-card.--video{
                    min-width: 25.2rem;
    
                    width: 100%;
                    
                    .wrap-img{
                        height: 13rem;
                    }
    
                    .content{
                        padding: 1.6rem 2rem 2.4rem 2rem;
    
                        strong{
                            font-size: 1.4rem;
                            padding-left: 5.8rem;
                        }
    
                        .icon-play{
                            width: 2.8rem;
                            height: 2.8rem;
                            top: 2.3rem;
                        }
                    }
                }
            }
        }
    }
    
    
    .main-osp6111{
        .promotion-overview{
            border-radius: 10px 10px 0 0;

            &::after{
                width: 40.5rem;
                height: 40.5rem;
                top: 21rem;
                left: -14rem;
            }

            .video{
                padding: 4rem 0;

                .media-list{
                    margin-top: 0rem;
                    // padding-top: 2rem;

                    overflow-x: scroll;
                    
                    column-gap: 2rem;
                    padding: 3.4rem 2.2rem;

                }

                .btn-more{
                    margin-top: 0;
                }
            }
        }
    }
}