/**
** animation
** keyframes
** responsive
** etc common style attributes
*/

// * 🦄️animation 
@mixin animation ($delay, $duration, $name, $timefunction: initial) {
  animation-fill-mode: forwards;
  animation-name: $name;

  animation-delay: $delay;
  animation-duration: $duration;
  animation-timing-function: $timefunction;
}

// * 🦄️keyframes 
@mixin keyframe ($animation_name) {
  @keyframes #{$animation_name} {
    @content;
  }
}

@include keyframe(loading){
  // 로딩화면 애니메이션
  0%{
    background-position-x: 0%;
  }
  50%{
    background-position-x: 100%;
  }
  100%{
    background-position-x: 0%;
  }
}

@include keyframe (moveSideToSide){
  0%{
    transform: translateX(0px);
  }50%{
    transform: translateX(4px);
  }100%{
    transform: translateX(0px);
  }
}

// banner이미지 애니메이션
@include keyframe (bannerContRightToLeft) {
  from{
    transform: translateX(3rem);
  }
  to{
    transform: translateX(0);
  }
}
@include keyframe (fadeInSeventToTem){
  from{
    opacity: 0.7;
  }
  to{
    opacity: 1;
  }
}


@include keyframe (moveUp){
  from{
    opacity: 0.3;
    transform: translateY(10%);
  }to{
    opacity: 1;
    transform: translateY(0);
  }
}

@include keyframe (moveDown){
  from{
    opacity: 0.3;
    transform: translateY(-3px);
  }to{
    opacity: 1;
    transform: translateY(0);
  }
}

@include keyframe(cardUp){
  from{
    transform: translateY(4rem);
    opacity: 0;
  }
  to{
    transform: translateY(0rem);
    opacity: 1;
  }
}


@include keyframe (fadeIn){
  from{
    opacity: 0;
  }to{
    opacity: 1;
  }
}
@include keyframe (fadeOut){
  from{
    opacity: 1;
  }to{
    opacity: 0;
  }
}


// * responsive
@mixin mobile {
  @media screen and (min-width: #{$mobile-min}) and (max-width: #{$mobile-max}) {
    @content;
  }
}

@mixin desktop-max{
  @media screen and (max-width: #{$desk-min}) {
    @content;
  }
}

@mixin mobile-max{
  @media (max-width: #{$mobile-max}) {
      @content;
  }
}

@mixin mobile-min{
  @media (max-width: #{$mobile-min}) {
      @content;
  }
}


