// 배경에 있는 원들
@mixin style_background-01($c_left, $l, $l-t, $c_right, $r, $r-b){
  position: relative;
  // padding-top: 9.6rem;
  // padding-bottom: 20.5rem;
  background-color: $bg_gray_f;
  width: 100%;
  overflow: hidden;
  z-index: 0;
  &::before,
  &::after{
    position: absolute;
    content: '';
    height: 100%;
    background-repeat: no-repeat;
    z-index: -1;
  }
  &::before{ // 왼쪽 원
    top: $l-t;
    left: $l;
    width: $c_left;
    height: $c_left;
    background-image: url('../../../public/asset/images/res/bg_circle_1.svg');
    background-size: $c_left;
  }
  &::after{ // 오른쪽 원
    bottom: $r-b;
    right: $r;
    width: $c_right;
    height: $c_right;
    background-size: $c_right;
    background-image: url('../../../public/asset/images/res/bg_circle_2.svg');
  }
}

@mixin grow_fontw($color, $time){
  text-shadow: 0 0 0 rgba($color, 0);
  transition: text-shadow $time ease-out;
  &.focused, &:hover{
    color: $color;
    text-shadow: 
    0 -0.2px 0 $color,
    -0.2px 0 0 $color,
    0.2px 0 0 $color;
  }
}

@mixin board_grid_mypage(){
  display: grid;
  grid-template-columns: 6.4rem 1002px 152px 106px;
  align-items: center;
  gap: 8px;
  
}

@mixin board_grid_notice(){
  display: grid;
  grid-template-columns: 12% 76.5% 10%;
  align-items: center;
  gap: 8px;

}
@mixin board_grid_question(){
  display: grid;
  grid-template-columns: 12% 86%;
  grid-template-areas: 'tit tit' 'cnt cnt';
  align-items: center;
  gap: 8px;

  .cate{
    cursor: none;
    font-size: 1.4rem;
    padding: 1.1rem 2.2rem;
    background-color: #eaeaea;
    border-radius: 3rem;
    background-color: #CFE9FD;
    &.--grsd{
      background-color: #CCF7F7;
    }
    &.--srds{
      background-color: #D3FFDF;
    }
    &.--etc{
      background-color: #EAEAEA;
    }
  }
}


@mixin style_header_gnb_a($color, $time, $hover_color:$color){
  position: relative;
  transition: all 0.3s;
  color: $color;

  text-shadow: 0 0 0 rgba($hover_color, 0);
  transition: text-shadow $time ease-out;
  
  &::after{
    position: absolute;
    content: '';
    bottom: -1rem;
    // bottom: 0;
    right: 0;
    display: block;
    width: 100%;
    height: 2px;
    transform: scaleX(0);
    transform-origin: left;

    background-color: $hover_color;
  }

  &.focused::after {
    visibility: hidden;
  }

  &.focused:hover::after, &:hover::after{
    visibility: visible;
    transform: scaleX(1);
    transition: transform 0.2s ease-in;
  }

  &.focused, &:hover{
    color: $hover_color;
    text-shadow: 
    0 -0.1px 0 $hover_color,
    // -0.1px 0 0 $hover_color,
    0.1px 0 0 $hover_color;
  }
}

