@import '../modules';

// pagination 공통 속성
.pagination{
  display: flex;
  justify-content: center;
  align-items: center;
  
  // arrow 방향 키 공통속성
  .btn-ar{
    width: 2rem;
    height: 2.6rem;

    // 왼쪽 방향일 때, 회전시킴
    &.--left{
      margin-left: 8px;
      transform: rotate(180deg);
    }
    // 오른쪽 방향일 때,
    &.--right{
      margin-right: 8px;
    }
    
    // 버튼 클릭 불가능
    &.--disable{
      pointer-events: none;
    }

    // 버튼 클릭 가능
    &.--able{
      svg path{
        fill: #797979;
        transition: fill 0.3s;
      }
    }

    .svg{
      width: 2rem;
      height: 2.6rem;
    }
  }

  // pagination 숫자들
  .pagination-nums{
    display: inline-flex;
    align-items: center;
    padding: 0 16px;

    li button{
      width: 3.6rem;
      height: 3rem;
      color: $font_color-797979_grey;

      &:hover{
        text-decoration: underline;
      }
    }

    // 현재 페이지 숫자
    .now{
      button{
        color: $azure;
        font-weight: 700;
      }
    }
  }
}

// pagination 구름 커뮤니티
.pagination.commu{
  transform: translateY(-1rem);
  ol li.now button{
    color: $commu_cyan_01;
  }
}


// * 버튼 - 목록보기
.btn-link-list{
  display: inline-flex;
  align-items: center;
  height: 5.8rem;
  padding: 0 4.4rem;
  border-radius: 4rem;

  color: #666666;
  border: 1px solid #B0B0B0;

  background-color: white;
  transition: all 0.2s;
  
  .icon{
    margin-right: 1rem;
  }

  // 동작정의
  &:hover{
    color: #3C3C3C;
    border: 1px solid #666666;
  }

  &:active{
    color: #212121;
    border: 1px solid #3C3C3C;
  }
}


// * 👽️responsive
@include mobile-max{
  .pagination{
    width: 100%;

    .btn-ar{
      
      .svg{
        width: 1.6rem;  
        height: 2.1rem;

      }
    }

    .pagination-nums{
      padding: 0 12px;
      column-gap: 0.2rem;
    }
  }

  .btn-link-list{
    height: 3.6rem;
    padding: 0 2.6rem;

    font-size: 1.2rem;

    .icon{
      width: 1.8rem;
      height: 1.8rem;
    }
  }

}