@import '../modules';

// * 구름 커뮤니티 게시판 
// 공통 속성


// 게시판 리스트 
.commu{

  .board-wrap{
    text-align: center;
    background-color: $white;
    border-radius: 3rem;
    box-shadow: 0px 5px 20px rgba(45, 52, 74, 0.03);

    // 게시판 상단
    .title{
      display: grid;
      grid-template-columns: 91.2rem 13.6rem 15.2rem 12.4rem;
      align-items: center;
      vertical-align: middle;
      column-gap: 8px;
      justify-content: center;

      span{
      @include style_font(500, 1.6rem, 1, $font_color-797979_grey);
        padding: 1.2rem;
        height: 4rem;
        background-color: $bg_color-F8F8F8-grey;
        border-radius: 5px;
        i.icon{
          vertical-align: top;
          height: 1.6rem;
          margin-left: 6px;
        }
      }
    }

    // 게시판 내용
    .cont-ul{
  
      li{
        display: grid;
        grid-template-columns: 91.2rem 13.6rem 15.2rem 12.4rem;
        align-items: center;
        vertical-align: middle;
        column-gap: 8px;
        justify-content: center;

        border-bottom: 1px solid #ececec;

        a{
          display: flex;
          align-items: center;
          padding: 1rem 0;
          text-align: left;

          height: 9.4rem;

          h4{
            padding-left: 2.5rem;
            padding-right: 8px;
            @include style_font(500, 2rem, 1, $font_color-212121_grey);
          }

          .views{
            margin-right: 6px;
            color: $commu_cyan_01;
          }
          
          .icon{
            width: 2.4rem;
            height: 2.4rem;
            margin: 0 2px;
          }
        }
      }
    }
  }

  // 버튼 - 글쓰기
  .btn-write{
    margin-top: 4rem;
    margin-left: auto;
    
    .icon{
      width: 1.6rem;  
      height: 1.8rem;
      margin-left: 0;
      margin-right: 1rem;
      background-image: none;
      svg path{
        fill: white;
      }
    }
  }

}


// 게시판 상세 내용
.commu{
  
  // 공통 속성
  .board-detail__warpper{
    padding: 4.4rem;
    box-shadow: 0px 15px 40px rgba(45, 52, 74, 0.1);
    border-radius: 3rem;

    .icon{
      width: 2rem;
      height: 2rem;
      vertical-align: middle;
      // 아이콘 영역 보기 
      // background-color: rgba($color: #000000, $alpha: 0.1);
    }

    span, button{
      @include style_font(400, 1.6rem, 1, #B0B0B0);
      letter-spacing: 0;
    }

    strong{
      color: $commu_cyan_01;
    }

    p{
      @include style_font(400, 1.6rem, 2.2em, #4b4b4b);
      letter-spacing: 0;
    }
  }

  .board-detail__warpper{

    .cont{

      .cont-title{
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        
        padding-top: 1rem;
        padding-bottom: 2.4rem;
        margin: 0 2rem;

        .cont-title--left{
          position: relative;
          padding-left: 4.8rem;

          h4{
            @include style_font(700, 2rem, 1, $font_color-212121_grey);
            padding-bottom: 1rem;

            // 공지사항 마크 
            .icon-table-notice{
              position: absolute;
              top: -0.2em;
              left: 0;
              width: 2.8rem;
              height: 2.8rem;
            }
          }

          .cont-title--detail{
            padding-top: 1rem;

            span{
              padding-left: 2rem;
            }

            // 관리자일때,
            .auth{
              position: relative;
              padding-right: 2rem;
              padding-left: 0;
              
              .icon{
                width: 2.4rem;
                height: 2.4rem;
                vertical-align: middle;
                margin-right: 6px;
              }
              // 구분선
              &::after{
                position: absolute;
                top: 0.2em;
                right: 0;
                content: '';
                width: 1px;
                height: 1em;
                background-color: #B0B0B0;
              }
            }
          }
        }
        
        .cont-title--right{
          text-align: right;

          .right-top{
            padding-bottom: 1.5rem;
            
            span{
              color: #888;
              margin-left: 2rem;
            }

            .icon{
              vertical-align: middle;
              margin-right: 1rem;
              opacity: 0.2;
            }
          }

          .right-bottom{
            position: relative;

            button{
              margin-left: 2rem;
              color: $commu_cyan_01;

            }

            .icon{
              margin-right: 1rem;
            }

            // sns 공유 버튼 hover시
            .sns-depth2{
              position: absolute;
              top: 3.6rem;
              right: -2rem;
              width: 31rem;
              padding: 3rem 3.2rem;
              text-align: center;
              box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.1);
              background-color: white;
              border-radius: 2rem;
              p{
                @include style_font(400, 1.6rem, 1, #666666);
                margin-bottom: 2rem;
              }

              // icon들
              div.sns{
                button{
                  width: 3rem;
                  height: 3rem;
                  margin: 0 1rem;
                  i.icon{
                    width: inherit;
                    height: inherit;
                  }
                }
              }
            }
          }
        }
      }

      .cont-detail{
        border-top: 1px solid #ECECEC;
        padding: 4.4rem 4.8rem;
        background-color: $bg_gray_f;
        // 내용
        p{margin-bottom: 4.4rem;}
      }
    }



    .commt__wrapper{
      
      .commt-title{
        margin-top: 1.9rem;
        padding: 2.8rem 2rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        h4{
          @include style_font(500, 2.4rem, 1 ,$font_color-212121_grey);
        }
      }

      // 댓글 레이아웃 공통 
      ol{

        li{
          position: relative;
        }

        // 댓글 상단
        div.--t{
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-bottom: 1rem;
          
          // 사용자 정보(이름, 등)
          span.commt-auth{
            position: relative;
            @include style_font(700, 1.8rem, 1, #666666);
            // 관리자 프로필 아이콘
            i.icon{
              position: absolute;
              left: -3.2rem;
              width: 2.4rem;
              height: 2.4rem;
            }
            // 작성자일 경우 옆에 뜨는 상태 아이콘 
            span.is-auther{
              padding: 4px 7px;
              color: white;
              font-size: 1.4rem;
              background-color: #F86848;
              border-radius: 5rem;
              margin-left: 6px;
            }
          }
        }

        // 댓글 정보 - 일시, 답글쓰기 버튼
        div.commt-inf{
          padding-top: 1rem;
          // padding-bottom: 4.4rem;
          padding-bottom: 2.4rem;
          button.btn-update-commt{
            margin-left: 2rem;
            span{
              color: #47B7D6;
              i.icon{
                margin-right: 1rem;
              }
            }
          } 
        }
      }
  
      // depth가 1인 댓글
      ol.commt-depth1{
        // padding-top: 2.4rem;
        padding-right: 2rem;
        margin-bottom: 2.4rem;
        
        .depth1-li{
          padding-top: 2.4rem;
          border-bottom: 1px solid #ECECEC;
        }
        
        li:last-child{
          border-bottom: none;
        }

        div.depth1-li__container{
          padding-left: 5rem;
          div.--t{
            padding-right: 4rem;
          }
          
          
          // -- depth1-li__container의 댓글은 padding값이 조금 더 들어가있어서... 
          .open-box{
            top: 5.4rem;
            right: 4.2rem;
          }
        }
      }
      // depth가 2인 댓글
      ol.commt-dep2{
        padding-left: calc(2.8rem + 5rem);
        padding-right: 4.4rem;
        padding-top: 4.4rem;

        margin-top: 2rem;
        background-color: $bg_gray_f;

        > li{
          padding-top: 0;
          padding-bottom: 2rem;
        }
        p{
          padding-bottom: 3.4rem;
        }


        // 첨부파일 정보
        div.attch-infor{
          margin-bottom: 3.4rem;
        }
      }
    }

    // * commut-update 게시판 댓글 작성 
    div.commt-update{
      position: relative;
      padding: 4.4rem 4.8rem;
      border: 1px solid #DCDDDE;
      background-color: white;
      // 사용자 이름
      span.user-n{
        @include style_font(700, 1.8rem, 1, #666666);
        margin-bottom: 2rem;
      }
      // 댓글 내용
      div.text-area{
        position: relative;
        width: 100%;
        height: 6.8rem;
        
        outline: 1px solid #D8D8D8;
        background-color: #FBFBFB;
        border-radius: 1rem;
        // 작성 영역
        textarea{
          @include style_font(400, 1.6rem, 1.4em, #91989D);
          &::placeholder{
            @include style_font(400, 1.6rem, 1.4em, #91989D);
          }
          padding: 2rem 2rem 0 2rem;
          width: 100%;
          height: 100%;

          background-color: transparent;
          border: none;

          resize: vertical;
        }

        // 작성 영역 늘리는 거 (아직 기능 없음)
        button.resizer{
          position: absolute;
          bottom: 0;
          left: 0;

          width: 100%;
          height: 1rem;

          text-align: center;
          background-color: #EAEDEF;
          border-radius: 0 0 1rem 1rem;

          cursor: s-resize;
          i.icon{
            vertical-align: 0.1em;
            height: 1rem;
          }
        }
      }


    }
  }

  // case별 속성
  .board-detail__warpper{
    // 1 비회원인 경우
    button.btn-commt-alert{
      display: block;
      width: 100%;
      height: 9.5rem;
      border: 1px solid #DCDDDE;
      margin-top: 2rem;

      color: #666666;
      i.icon{
        width: 2.4rem;
        height: 2.4rem;
        margin-right: 1rem;
      }
    }
    // 1 비회원인 경우 2
    button.btn-commt-alert.--commt{
      position: relative;
      background: #F8F9FA;
      border: none;
      margin-top: 0rem;
      margin-bottom: 2rem;

      i.icon-commu-comment{
        position: absolute;
        top: 3.6rem;
        left: 4.8rem;
      }
    }

    // 2 회원인 경우 - 대댓글
    div.commt-update-dep2{
      position: relative;
      background-color: #F8F9FA;
      padding: 4.4rem 4.4rem 4.4rem 9.2rem;
      i.icon-commu-comment{
        position: absolute;
        top: 4.4rem;
        left: 4.8rem;
      }
    }
  }


  ul.open-box{
    position: absolute;
    top: calc(2.2rem + 1rem);
    right: 0rem;

    padding: 6px;

    background-color: white;
    box-shadow: 0px 8px 20px rgba(15, 23, 42, 0.06);
    border: 1px solid #D8D8D8;
    border-radius: 1rem;
    li{
      margin-bottom: 4px;
      position: relative;
    }
    button{
      width: 14.8rem;
      height: 4.4rem;
      padding-left: 1rem;
      text-align: left;
      @include style_font(400, 1.6rem, 1, #91989D);
      &:hover{
        color: $commu_cyan_01;
      }
    }
  }



  // ! component 빼기
  // 버튼 - 첨부파일 리스트 열고 닫기 
  button.togg-attch{
    position: relative;
    margin-bottom: 1rem;
    span{
      margin: 0 1rem;
    }
    i.icon-dropbox{
      width: 1.4rem;
      height: 1.0rem;
    }
  } 

  // 첨부파일 리스트
  ul.attch-list{
    display: flex;
    gap: 1.4rem;
  }

  // 첨부 파일 입력하는 곳 
  div.commt-more{

    //  파일 첨부
    div.attch-file{
      position: relative;
      margin-top: 2.4rem;
      button.btn-basic-02{
        width: 10.6rem;
        height: 3.4rem;
        padding: 0;
        justify-content: center;
        align-items: center;
        font-size: 15px;
        margin-right: 2rem;
        i.icon{
          width: 2rem;
          height: 2rem;
        }
      }
      span.attch-span{
        color: #666666;
        font-size: 1.6rem;
      }
      span.file-size{
        position: absolute;
        right: 0;
        top: 1rem;
        color: #B0B0B0;
        font-size: 1.6rem;
      }
    }

    div.bottom{
      margin-top: 2rem;
      display: flex;
      justify-content: space-between;
      // 취소, 등록 버튼
      ul.attch-list{
        width: 80%;
      }

      button.btn{
        display: inline-block;
        margin-left: 1.4rem;
      }
    }

    // 글쓰기 페이지는 3파트로 나뉨
    .bottom.--commt{
      margin-top: 2.4rem;
      align-items: center;

      .commt-alarm{
        margin-top: 0;
        color: #666666;
      }
    }
  }


  // 첨부파일 버튼들(누르면 다운받을 수 있음)
  .down-attch{
    padding: 0.6rem 1rem;
    height: 3.4rem;
    
    display: inline-flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 6px;
    background-color: #E7EBEE;
    i.icon{
      width: 2rem;
      height: 2rem;
      margin-right: 1rem;
    }
    button.btn-close{
      i.icon{
        margin-right: 0;
        margin-left: 1rem;
      }
    }
    span{
      color: #47B7D6;
    }
  }

  // 첨부파일 취소 
  a.down-attch.--cancle{
    i.icon-img{
      background-image: none;
      svg path{
        fill: #B0B0B0;
      }
    }
    span{color: #B0B0B0;}
  }
  
  
  // 토글 버튼 - 댓글 알림
  .commt-alarm{
  
    span{
      margin-right: 1rem;
      color: #666666;
    }
  
    // 버튼
    .commt-alarm-toggle{
      position: relative;
      width: 3.6rem;
      // width: 5rem;
      height: 2rem;
      padding: 0.33rem;
      
      vertical-align: middle;
      border-radius: 2rem;
      background-color: #B0B0B0;
      transition: background-color 0.3s;
    
      &::after{
        position: absolute;
        content: '';
        // top: 0.33rem;
        top: 0;
        left: 0.33rem;
        width: 1.33rem;
        height: 1.33rem;
        border-radius: 50%;
        background-color: white;
        transform: translateX(0) translateY(25%);
        transition: transform 0.3s;
      }
      &.active {
        background-color: #0AB4E2;
        &::after{
          transform: translateX(120%) translateY(25%);
        }
      }
    }
  }
}


@include mobile-max{
  .commu{
    .board-wrap{
      padding: 2rem 0;
      border-radius: 1.5rem;

      .title{
        display: none;
      }

      .cont-ul{
        position: relative;
        display: flex;
        flex-direction: column;

        li{
          width: 100%;
          height: 8.7rem;
          padding: 2rem;
          justify-content: space-between;

          grid-template-columns: 10% 40% 15% 15%;
          grid-template-areas: 
          "tit tit tit tit"
          "view . auth date"
          ;

          span{
            font-size: 1.2rem;
            color: $gray4;
          }

          .cont-tit{
            width: 100%;
            grid-area: tit;
            padding: 0;
            height: auto;
            
            .icon-table-notice{
              display: none;
            }
            
            h4{
              padding: 0;
              font-size: 1.4rem;
            }

            .icon{
              width: 1.4rem;
              height: 1.4rem;
            }
          }

          .cont-auth{
            grid-area: auth;
            justify-self: flex-end;
          }

          .cont-date{
            grid-area: date;
          }

          .cont-views{
            grid-area: view;
          }
        }
      }
    }
  }
}
