// 작성자: 정수진C
@import './../modules';


.value{ // 값을 입력하는 공간 
  @include style_font(500, 1.6rem, 1, #91989D);
  height: 5.8rem;
  width: 100%;
  padding: 1.6rem 2rem;

  border: none;
  background: #FBFBFB;
  outline: 1px solid #D8D8D8;
  border-radius: 1rem;
  &::placeholder{@include style_font(500, 1.6rem, 1, #91989D);}
  &:focus{
    outline: 3px solid $azure;
  }
}

.value.commu{
  &:focus{
    outline: 3px solid $commu_cyan_01;
  }
}


// * DropDown selectbox
.dropbox{
  position: relative;
  width: 100%;

  input{
    position: absolute;
    left: 2.1rem;
    width: 20rem;
    font-weight: 500;

    background-color: transparent;
    opacity: 0;
  }

  .value{
    text-align: right;
    
    .icon{
      vertical-align: middle;
      width: 1.3rem;
      height: 100%;
      vertical-align: -1.4em;
    }
  }

  .value.focused{
    outline: 3px solid $azure;
  }


  &.typed.selected button{
    outline: 1px solid #D8D8D8;
  }



  label{
    @include style_font(500, 1.6rem, 1, #91989D);
    transition: all 0.3s;
    position: absolute;
    top: 2.1rem;
    left: 2.1rem;
    user-select: none;
  }

}

.dropdown{
  background-color: white;
  border: 1px solid #D8D8D8;
  border-radius: 1rem;
  box-shadow: 0px 8px 20px rgba(15, 23, 42, 0.06);
  z-index: 9;
  ol{
    padding: 6px 0.6rem;
    display: flex;
    flex-direction: column;
    gap: 4px;
    li button{
      text-align: left;
      padding: 1rem 1rem;
      width: 100%;
      @include style_font(400, 1.6rem, 1.4em, #666666);
      &:hover{
        color: $azure;
      }
    }
  }
}


.textfield{
  label{
    @include style_font(500, 1.6rem, 1, #91989D);
    transition: all 0.3s;
    position: absolute;
    top: 2.1rem;
    left: 2.1rem;
    user-select: none;
  }
}


// textfield - type - 2
.textfield.--t2{
  textarea.value{
    @include style_font(500, 1.6rem, 1, #91989D);
    vertical-align: text-top;
    height: 50rem;
  }
}

// 타이핑 하고 있을 때,

.form-ul{
  // textfield - type - 1
  .textfield.--t1.typed{

    label{
      transform: translateY(-1rem);
      visibility: visible;
      font-size: 1.2rem;
    }

    input{
      padding-top: 3rem;
      padding-bottom: 1rem;
      color: #212121;
      font-size: 1.6rem;
      opacity: 1;
    }
  }


  .textfield.--t2.typed{

    .value{
      color: #212121;
    }
  }
}
