@import './../modules';

header{
  position: fixed;  
  width: 100%;
  transition: background-color 0.3s;
  z-index: 999;

  .gnb__depth1{
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .--left,
    .--right{
      display: inline-flex;
      justify-content: space-between;
      flex-wrap: nowrap;
    }

    .depth1__logo{
      display: inline-block;
      width: 100%;

      .logo{
        width: 14rem;
        height: 8.4rem;
        background-size: contain;
      }

      .logo::after{
        display: none;
      }
    }

    ul{
      display: inline-flex;
      align-items: center;

      li{
        width: max-content;
      }
      a{
        font-weight: 500;
      }
    }

    .gnb-depth1{
      margin-left: 2.8rem;
      li{
        margin: 0 3rem;
      }
    }

    .depth1__community{
      gap: 5.8rem;
      margin-right: 6.3rem;
    }

    .depth1__user{
      gap: 2rem;

      a{
        @include style_font(400, 1.4rem, 1, #787878);
        margin: 0;
        
        &::after{
          // 밑에 줄 없애는거 
          display: none;
        }
        &.focused, &:hover{
          color: #666;
          font-weight: 500;
        }
      }
    }

    // hamberger
    .depth1__box{
      display: none;

      .btn-toggle-depth1{
        i.icon{
          width: 2rem;  
          height: 2rem;
        }
        // display: none;
      }
    }
  }

  // depth2
  .gnb__depth2{
    width: 100%;
    position: fixed;

    .depth2{
      color: $white;
      background-color: $royalazure;

      .depth2-container{
        display: flex;
        padding: 7.6rem 0;
        height: 38rem;

        div{
          margin-right: 239px;

          h5{
            padding-left: 10px;
            font-weight: 700;
            font-size: 24px;
            padding-bottom: 0.5em;
          }
          p{
            width: 266px;
            height: 15rem;
            padding-left: 10px;
            padding-bottom: 3em;
            @include style_font(300, 1.8rem, 1.8em, white);
          }
          button{
            width: 208px;
            height: 47px;
            background: #FFFFFF;
            border-radius: 5rem;
            font-weight: 700;
            color: $royalazure;
          }
        }

        ul{
          display: flex;
          flex-wrap: wrap;
          justify-content: flex-start;

          li{
            width: 386px;
            padding-bottom: 1em;
            margin: 20px 48px 40px 0;
            
            font-weight: 700;
            border-bottom: 1px solid rgba(255, 255, 255, 0.07);
            a{
              color: $white;
              
            }
          }
        }
      }

    }

    // 구름 커뮤니티
    .depth2:nth-child(5){
      background-color: $commu_cyan_03;
      display: none;
    }

    // * 🦄️Interaction
    .depth2{
      display: none;
    }

    .depth2-open{
      display: block;
      @include animation(0s, 0.6s, fadeIn);
    }
    
    .depth2-close{
      @include animation(0s, 0.3s, fadeOut);
    }
    
  }

}

.header-main{
  background-color: transparent;

  .gnb__depth1{

    .gnb-depth1 li a,
    .depth1__community li a{
      @include style_header_gnb_a(white, 0.3s);
      padding: 0.5rem 0; 
      height: 2rem;

      color: $white;
    }

    .depth1__community svg path{
      fill: $white;
    } 

    .depth1__user a{
      color: rgba(255, 255, 255, 0.58);
      &:hover{
        color: rgba(255, 255, 255, 1);
      }
    }
  }
  
}


.header-sub{
  background-color: $white;
  
  .gnb__depth1 {
    a{
      @include style_header_gnb_a($black_0, 0.3s, $ultramarine);
    }
    
    .depth1__community{
      li:first-child a{
        &.focused, &:hover{
          @include style_header_gnb_a($black_0, 0.3s, $commu_cyan_03);
        }
        &::after{
          background-color: $commu_cyan_03;
        }
      }

      // 버튼 - 인재채용
      li:last-child a{
        svg path{
          fill: #212121;
        }
        &:hover svg path{
          transition: all 0.3s;
          fill: $ultramarine;
        }
      }
    }

    .depth1__box{
      .btn-toggle-depth1{
        i.icon svg path{
          fill: #212121;
        }
      }
    }

  }
}


// responsive - lnb
.lnb{
  position: absolute;
  top: 0;
  width: 100%;
  background-color: $white;
  z-index: -1;
  
  padding-top: 4.8rem;

  // shadow
  &::after{
    content: '';
    position: absolute;
    display: block;
    width: 100vw;
    height: 100vh;

    background-color: rgba($color: #000000, $alpha: 0.5);
    z-index: 1;
  }

  .lnb-depth1{
    display: flex;
    flex-direction: column;

    .btn-lnb-depth1{
      border-bottom: 1px solid #F2F2F2;
      height: 7rem;
      color: $gray2;
      font-size: 1.4rem;
      font-weight: 400;
      width: 100%;
      // height: 100%;
      display: inline-flex;
      align-items: center;
      justify-content: space-between;

      padding: 0 3.6rem 0 2.4rem;

      .icon{
        width: 1.6rem;
        height: 1.6rem;
      }
    }

    .btn-lnb-depth1.--hr{
      justify-content: flex-start;
      svg path{
        fill: $gray2;
      }
    }
  }

  .lnb-depth2 {
    // visibility: hidden;
    // display: none;

    background-color: $bg_gray_f;

    li .btn-lnb-depth2{
      display: inline-flex;
      align-items: center;

      width: 100%;
      height: 5.6rem;
      padding-left: 2.4rem;

      color: $gray2;

    }
  }

}

.lnb{
  li.focused{
    .btn-lnb-depth1{
      color: $azure;
    }

    // .lnb-depth2{
    //   display: block;
    //   visibility: visible;
    // }
  }
}



// * 👽️responsive
@include mobile-max{
  header{
    nav{
      width: 100%;
    }
    
    .gnb__depth1{
      padding: 0 1.6rem;
      height: 4.8rem;
      
      .depth1__logo{
        .logo{
          width: 7rem;
          height: 1.6rem;
        }
        
      }
      .gnb-depth1,
      .depth1__community,
      .depth1__user{
        display: none;
      }

      // button hamberger - responsive 
      .depth1__box{
        display: block;
      }
    }
  }
}
