@import '../../modules';

// 공지사항 페이지
.section-osp7116{
  padding-top: 14.6rem;

  .board-wrap{
    padding-top: 0;
    padding-bottom: 9.6rem;
    box-shadow: none;

    .title{
      span{
        height: 4rem;
        text-align: center;
        background-color: $bg_color-F8F8F8-grey;
        padding: 1rem 0;
        @include style_font(500, 1.6rem, 1.2em, $font_color-797979_grey);
        border-radius: 5px;
      }
    }

    ul.cont-ul{
      padding: 0;
    }
  }
} 


@include mobile-max{
  .section-osp7116{
    padding-top: 6.8rem;

    .board-wrap{
      padding-bottom: 4.8rem;
    }
  }
}