// 작성자: 정수진C
@import '../modules';


// * search-banner - 배너에 있는 검색창
.search-banner{
  // 기준 1440
  position: absolute;
  bottom: 9.7rem;
  right: 2.8rem;
  z-index: 99;

  label{
    position: absolute;
    top: 1.6rem;
    left: 1.6rem;
    z-index: 1;
    .icon-search{
      padding: 0.3rem;
      width: 2.8rem;
      height: 2.8rem;
      margin-right: 1.2rem;
      svg{
        width: 2.2rem;
      }
    }
  }


  label .icon-search.focused svg path{
    fill: $azure;
    transition: fill 0.3s;
  }

  input[name="search"]{
    position: relative;
    width: 44.9rem;
    height: 5.8rem;
    padding: 1.6rem 2rem 1.6rem 6.1rem;
    border-radius: 10rem;

    color: #91989D;
    transition: outline 0.3s;
    outline: 3px solid rgba(16, 49, 157, 0);
    &:hover{
      outline: 3px solid rgba(16, 49, 157, 0.1);
    }
    &:active{
      outline: 3px solid rgba(16, 49, 157, 0.4);
    }
    &:focus{
      outline: 3px solid rgba(16, 49, 157, 0.3);
    }
    &.typing{
      // outline: 3px solid rgba(16, 49, 157, 0.3);
      @include style_font(700, 1.8rem, 1, $azure);
    }
  }
  .btn-close{
    position: absolute;
    right: 2rem;
    top: 2rem;

    width: 2rem;
    height: 2rem;
  }
}

.search-banner.commu{
  label .icon-search.focused svg path{
    fill: $commu_cyan_01;
  }
  input[name='search']{
    outline: 3px solid rgba(0, 140, 178, 0.0);

    &:active{
      outline: 3px solid rgba(0, 140, 178, 0.4);
    }
    &:focus{
      outline: 3px solid rgba(0, 140, 178, 0.3);
    }
    &.typing{
      @include style_font(700, 1.8rem, 1, $font_color-212121_grey);
      // outline: 3px solid rgba(0, 140, 178, 0.0);
    }
  }
}



// * search table - 게시판에 있는 검색창
.search-table{
  position: relative;
  margin-left: auto; // 오른쪽 정렬
  width: 35.7rem;

  label{
    position: absolute;
    top: 1.3rem;
    left: 1.6rem;
    .icon-search{
      width: 2.2rem;
      height: 2.2rem;
      &.focused svg path{
        fill: $azure;
      }
    }
  }

  input[name='search-table']{
    width: 100%;
    padding: 1.2rem 0 1.2rem 4.7rem;
    color: #91989D;
    font-size: 1.6rem;
    border: 1px solid #D8D8D8;
    border-radius: 1rem;
    background: #FBFBFB;
    transition: border 0.3s;
    &.typing{
      border: 1px solid $azure;
      @include style_font(400, 1.6rem, 1, $azure);
    }
    &:focus{
      border: 1px solid $azure;
    }
  }

  // 닫기 버튼
  button.btn-close{
    position: absolute;
    top: 1.4rem;
    right: 2rem;
    width: 2rem;
    height: 2rem;
  }
}

.search-table.commu{
  label .icon-search.focused svg path{
    fill: $commu_cyan_01;
  }

  input[name="search-table"]{
    &.typing{
      @include style_font(700, 1.6rem, 1, $font_color-212121_grey);
      border: 1px solid $commu_cyan_01;
    }
    &:focus{
      border: 1px solid $commu_cyan_01;
    }
  }
}
