// 작성자: 정수진C
@import '../modules';


// 뒷 배경 까맣게 하기
div.popup-wrapper{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 9;
  display: flex;
  justify-content: center;
  align-items: center;

  // 팝업 창
  div.popup-container{
    width: 42.4rem;
    height: 21.9rem;

    padding: 3rem 3rem 2rem 3rem;

    background-color: white;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.1);
    border-radius: 2rem;
  }

  // 팝업 창 윗 부분
  div.--top{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 3rem;
    // alert 종류
    span.popup-span{
      @include style_font(700, 2rem, 1, #212121);
    }
    // 닫기 버튼
    button.btn-close{
      align-self: flex-start;
      i.icon{
        width: 2rem;
        height: 2rem;
      }
    }
  }

  // 팝업 창 안내 부분
  p.popup-p{
    @include style_font(400, 1.6rem, 1.6em, #666);
    padding: 1.8rem 0 1rem 0;
    text-align: left;
  }
  // 팝업 창 아랫 부분 버튼들
  div.--bottom{
    padding: 2rem 0;
    text-align: right;
    // button 공통 속성
    button{
      padding: 0.8rem 1.2rem;
      @include style_font(700, 1.8rem, 1, #B0B0B0);
    }
    button.btn-delete{
      margin-left: 2rem;
      color: $commu_cyan_01;
    }
  }
}