@import '../modules';

// 새로운 소식 
.main-board{
  .board__container{
    text-align: center;
    padding-top: 9.5rem;
  }

  .board-cards{
    margin-top: 7.6rem;
    display: flex;
    // height: 289px;
    justify-content: space-between;

    button{
      position: relative;
      width: 335px;

      min-width: 21.6rem;


      // display: flex;
      // flex-direction: column;
      // justify-content: space-between;

      text-align: left;

      img{
        width: 100%;
        height: 195px;
        border-radius: 1rem;

        background-color: dimgray;
      }

      .board-title{
        width: 100%;
        margin-top: 1rem;

        word-break: break-all;
        white-space: normal;

        @include style_font(500, 2rem, 1.5, black);
      }
    }
  }

  .btn-more{
    margin: 10rem 0;
  }
}

// 뉴스룸
.main-news{
  padding-top: 9.6rem;
  background: $bg_gray_f;
  text-align: center;
  
  .news-grid{
    margin-top: 7.6rem;
    position: relative;

    display: grid;
    grid-template-columns: 830px 577px;
    justify-content: center;

    column-gap: 3.2rem;
    row-gap: 3rem;

    // news 공통
    button{
      text-align: left;
      border-radius: 10px;
      background-color: white;

      .news-content{

        .news-title{
          @include style_font(500, 2.4rem, 1.5, black);

          // 두 줄부터 ... 해야해용
          // white-space: nowrap;

          overflow: hidden;
          text-overflow: ellipsis;
        }

        .news-date{
          @include style_font(300, 1.8rem, 1.7, $font_color-B0B0B0_grey);
          margin-top: 14px;
        }        
      }
    }

    // 첫 번째 news 
    .top-news{
      display: flex;
      flex-direction: column;
      justify-content: space-between;  

      height: 100%;

      grid-row-start: 1;
      grid-row-end: 4;

      img{
        width: 100%;
        height: 439px;
        border-radius: 10px;
      }

      .news-content{
        position: relative;
        padding: 3.1rem 4rem 4rem 4rem ;


        width: 100%;
        height: 100%;

        .news-title{
          @include style_font(700, 2.6rem, 1.5, black);
          height: 55px;
          margin-bottom: 8px;
        }

        p{
          line-height: 170%;
          width: 100%;
          letter-spacing: -0.5px;
          color: #666666;
        }

        .news-date{
          color: #b0b0b0;
        }
      }
    }

    // 다른 news 
    .news-another{
      display: flex;
      
      .news-content{
        padding: 4rem 3rem 4rem 4rem ;

        .news-title{
          height: 77px;

        }
      }

      img{
        display: inline-block;
        width: 20.8rem;
        height: 100%;
      }
    }
  }

  .btn-more{
    margin: 10rem 0;
  }

}

// 배너 - 고객문의
.main-customer{

  .customer__container{
    position: relative;
    // height: 35.7rem;
    height: 29.9rem;
    // display: flex;
    margin-top: 58px;

    background: linear-gradient(90.72deg, #4661FF 0.62%, #4BA5FF 99.38%);
    border-radius: 30px;

    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    // align-items: center;

    // padding-right: 16rem;
    // padding-top: 10.9rem;
    // padding-bottom: 6.9rem;
    // padding: 7rem 16rem;

    // 이미지 
    .img-customer-banner{
      position: absolute;
      bottom: 0;
      left: 13.4rem;
      width: 28.9rem;
      height: 35.7rem;
    }

    // 내용 
    .banner-title{
      // padding: 1em 8.6rem;

      h5{
        @include style_font(700, 3.6rem, 1.5, $white);
        text-align: left;
        padding-bottom: 3.9rem;
      }

      // p{
      //   @include style_font(400, 2.4rem, 1.5, $white);
      // }
    }

    .btn{
      text-align: center;
      width: 280px;
      height: 76px;

      background: $white;
      border-radius: 48px;
      font-size: 24px;
    }
  }
}

.temp-layout{
  padding-bottom: 2rem;
  padding-right: 20.2rem;
  text-align: left;

  .inquiry-content{
    position: relative;
    padding-left: 12rem;
    padding-bottom: 2.2rem;

    .title{
      position: absolute;
      left: 0;

      display: inline-flex;
      justify-content: center;
      align-items: center;

      width: 11.7rem;
      height: 3rem;
      border-radius: 10rem;
      color: $white;

      font-size: 1.4rem;

      background-color: rgba($royalazure, 0.2);
    }

    .contact-number,
    .contact-email{
      font-size: 2.8rem;
      font-weight: 700;

      color: $white;
      padding-left: 1.8rem;
      padding-bottom: 1rem;
    }

    p{
      padding-left: 1.8rem;
      color: $white;
      font-size: 1.4rem;
    }
  }
}


// * 👽️responsive
@include mobile-max{

  // 새로운 소식 
  .main-board{
    .board__container{
      padding-top: 4rem;
    }

    .board-cards{
      overflow-x: scroll;
      overflow-y: hidden;   
      white-space: nowrap;
      
      margin-top: 4rem;
      padding: 0 2rem;

      gap: 1.2rem;

      button{

        img{
          height: 12.6rem;
        }

        .board-title{
          font-size: 1.4rem;
        }
      }
    }

    .btn-more{
      margin: 3.4rem 0 4rem 0;
    }
  }

  // 뉴스룸
  .main-news{
    padding: 4rem 2rem 0 2rem;

    .news-grid{
      margin-top: 3.4rem;

      grid-template-columns: 100% ;
      row-gap: 1.2rem;

      // news 공통
      .top-news,
      .news-another{
        .news-content{
          position: relative;
          width: 100%;

          .news-title{
            @include style_font(500, 1.4rem, 1.5, black);
            height: auto;
          }
          .news-date{
            @include style_font(400, 1.2rem, 1.5, $font_color-B0B0B0_grey);
          }
        }
      }

      // 첫 번째 news
      .top-news{
        img{
          height: 17rem;
          border-radius: 1rem 1rem 0 0;
        }

        .news-content{
          padding: 2rem;

          .news-date{
            margin-top: 10px;
          }
        }
      }

      // 다른 news
      .news-another{

        .news-content{
          padding: 2rem;

          .news-title{
            overflow: hidden;
            white-space: nowrap;
            
            text-overflow: ellipsis;
          }

          .news-date{
            margin-top: 8px;
          }
        }

        img{
          display: none;
        }
      }
    }

    .btn-more{
      margin: 3.4rem 0 4rem 0;
    }
  }

  // 배너 - 고객문의
  .main-customer{
    .customer__container{
      flex-direction: column;
      justify-content: space-between;


      height: 22.3rem;

      margin-top: 0;
      padding: 0;
      padding-top: 2.6rem;
      padding-bottom: 3rem;
      padding-right: 3.3rem;
      padding-left: 4rem;

      border-radius: 0;

      // 이미지
      .img-customer-banner{
        width: 10.7rem;
        height: 13.2rem;
        left: 4rem;
      }
      
      .banner-title{
        align-self: flex-start;
        padding: 0;
        h5{
          font-size: 1.6rem;
          padding-bottom: 8px;
        }

        p{
          font-size: 1.4rem;
        }
      }

      
      .btn{
        font-size: 1.4rem;
        width: 14.2rem;
        height: 4rem;
        padding: 0;

        .icon{
          margin-left: 5px;
          width: 1.4rem;
          height: 1.6rem;
        }
      }
    }

  }
}
