@import '../../modules';


main.main-osp7121{
  position: relative;

  // 공통
  section{
    padding-top: 9.6rem;
    padding-bottom: 16.7rem
  }

  // 대문 글
  hgroup{
    @include style_font(700, 3.2rem, 1, $black_0);
    padding-bottom: 9.6rem;
    // border-bottom: 3px solid $grey_ec;
    text-align: center;

    strong{
      color: $azure;
    }
    h2{
      padding-bottom: 2rem;
    }
  }

  .form-layout{
    padding-top: 6.4rem;
    padding-bottom: 9.4rem;
    text-align: center;

    ul.form-ul{
      text-align: left;
      display: grid;
      gap: 1.6rem;
      // 가로 폭 설정 
      li.--w20{
        width: calc(26% - 5rem);
      }
      li.--w100{
        width: calc(100% - 5rem);
      }

      > li{
        position: relative;
        margin-left: 5rem;
        // list style (파란 점)
        &::before{
          position: absolute;
          content: '';
          top: 2.8rem;
          left: -3rem;
          width: 8px;
          height: 8px;
          background-color: $azure;
          border-radius: 50%;
        }


        // checkbox 
        .checkbox{
          text-align: left;
          transition: background-color 0.3s;

          &:focus{
            outline: 1px solid #D8D8D8;;
          }
          .agree-btns{
            display: flex;
            padding-bottom: 2rem;

            align-items: center;

            span{
              vertical-align: text-top;
              margin-left: 1rem;
            }
  
            .icon{
              width: 1.3rem;  
              height: 1rem;
              margin-left: auto;
            }
          }

          .agree-content{
            transition: height 3s;
          }

        }

        .checkbox:hover{
          input[type="checkbox"]{ 
            // border: 1px solid $azure;
            cursor: pointer;
          }
        }

        // checked
        .checkbox.checked{
          height: max-content;
          outline: 3px solid $azure;
          background-color: white;
          
          .agree-btns{
            border-bottom: 1px solid #D8D8D8;

            .icon{
              transform: rotate(180deg);
            }
          }

          .agree-content{
            @include style_font(500, 1.4rem, 1.7, black);
            
            padding-top: 2.4rem;
            padding-bottom: 7px;
          }

        }
      }

      li.invalid{
        position: relative;
        .value{
          outline: 1px solid #FF9C9C;
        }
        &::after{
          position: absolute;
          top: -1rem;
          right: 1.8rem;
          content: attr(data-message);
          font-size: 1.2rem;
          padding: 1.2rem;
          color: white;
          width: max-content;
          background-color: #e65050;
          border-radius: 3px;
        }
      }
    }
  }

  // div.dropdown{
  //   position: absolute;
  //   top: calc(100% + 8px);
  //   width: 100%;
  // }

  // 문의하기 버튼
  input[type="submit"]{
    width: 32rem; 
    height: 7.6rem;
    margin-top: 7.6rem;
    margin-bottom: 9.3rem;
    @include style_font(700, 2.4rem, 1, white);
  }

  // 문의하기 버튼 밑
  div.inquiry{
    border-top: 4px solid #ececec;
    padding-top: 4rem;
    text-align: center;
    span{
      @include style_font(400, 1.8rem, 1, #666);
    }
    span.title{
      font-weight: 700;
      padding-right: 1.4rem;
    }
    span:nth-child(2){
      padding-right: 5.2rem;
    }
  }

  // 문의하기 완료 팜업창
  section.result-bg{
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba($color: #000000, $alpha: 0.3);

    display: flex;
    justify-content: center;
    div.question-result{
      width: 42.4rem;
      height: 43.7rem;
      margin-top: 24rem;
      padding: 3rem;

      text-align: center;

      background-color: white;
      border-radius: 2rem;
      box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.1);
      div{text-align: right;}
      i.icon-mail{
        margin-top: 2.2rem;
        margin-bottom: 3.5rem;
        width: 8rem;
        height: 8.4rem;
      }
      h6{
        margin-bottom: 3.2rem;
        @include style_font(700, 2rem, 1, #3c3c3c);
      }
      p{
        margin-bottom: 6.6rem;
        @include style_font(400, 1.6rem, 1.6em, #666);
        strong{
          color: $azure;
        }
      }
      button{
        font-weight: 700;
        color: $azure;
        &.confirm{
          margin-right: 0.4rem;
        }
      }
    }
  }
  section.result-bg.in-active{
    display: none;
  }
  section.result-bg.active{
    display: flex;
  }

  .temp-layout-inquiry{
    display: flex;
    width: 100%;
    column-gap: 2rem;
    justify-content: space-between;


    .inquiry-content{
      flex: 1 1 50%;

      display: inline-flex;
      flex-direction: column;
      justify-content: space-evenly;
      align-items: center;

      height: 39rem;
      padding: 2rem 0;

      text-align: center;
      border-radius: 1rem;
      color: $azure_emp_01;
      background-color: $grey_f5;

      .icon-temp-inquiry-call{
        width: 13rem;
        height: 8.8rem;
      }
      .icon-temp-inquiry-email{
        width: 10rem;
        height: 10rem;
      }

      .title{
        display: inline-flex;
        align-items: center;
        justify-content: center;
        width: 12.5rem;
        height: 3.5rem;
        border-radius: 3rem;
        
        font-size: 1.8rem;
        line-height: 1;
        
        background-color: #DBEEFF;
      }

      .contact-number{
        font-size: 3.2rem;
        font-weight: 700;
        color: $azure_emp_01;
      }
      .contact-email{
        font-size: 2.8rem;
        font-weight: 700;
        color: $azure_emp_01;
      }

      p{
        max-width: 32.2rem;
        line-height: 1.6em;
        font-size: 1.6rem;
        color: #809EC0;
      }

    }
  }
}