@import 'scss-reset/_reset.scss'; 
@import 'modules', 'layout', 'common', 'pages';

::-webkit-scrollbar{
  width: 0;
  height: 0;
}

html{
  font-size: 10px;
  scroll-behavior: smooth;
}

body{
  $font-stack: 'NotoSansKR-Regular';
  @include font-face('NotoSansKR-Regular', 'NotoSansKR-Regular', 400);
  font: 100% $font-stack;

  font-size: 1.8rem;
  letter-spacing: -0.5px;
  line-height: 1;

  color: $black_0;

  a{
    text-decoration: none;
    outline: none;
    color:inherit;

    &:hover, &:active{
      text-decoration: none; 
      background-color:transparent;
    }
  }
  span, i{
    display: inline-block;
  }
  input{
    border: 0;  
  }

  input[type="checkbox"]{ 
    appearance: none;
    width: 2.4rem;
    height: 2.4rem;
    border: 1px solid #D8D8D8;
    border-radius: 3px;
    background-color: white;

    transition: all 0.3s;

    &:hover{
      border: 1px solid $azure;
    }

    &:checked{
      border: 1px solid $azure;

      background-color: $azure;
      background-image: url('../../public/asset/icon/icon_CheckBox.svg');
      background-size: cover;
      background-position: center;
    }
  }

  textarea{
    resize: none;
  }

  .svg{
    width: inherit;
    height: inherit;
  }
}

// *🍚️ theme
.App, .user{
  position: relative;
  min-width: 1440px;
  
  .w-default{ 
    width: $width_1440;
    margin: 0 auto;
  }

  .width1352{
    width: 135.2rem;
    margin: 0 auto;
  }

  .--mobileview{
    display: none;
  }

  .--h{
    visibility: hidden;
  }
  .--n{
    display: none;
  }

  // * global 지수빈
  .clear::after{
    clear: both;
    display: block;
    content: '';
  }
  .wrap-img{
    img{
      display: block;
      width: 100%;
      min-height: 100%;
    }
  }
  .date{
    color: #B0B0B0;
    font-size: 15px;
  }

  // * tab style1 
  div.tab-style_box1{
    display: flex;
    justify-content: center;
    align-items: center;

    padding-top: 9.6rem;
    padding-bottom: 3.2rem;
    ul{
      position: relative;
      display: flex;
      &::before{
        // 그냥 border주면 안맞음
        position: absolute;
        content: '';
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;

        border-radius: 1rem;
        border: 3px solid $grey_ec;
        z-index: -9;
      }
      li{
        width: 29.3rem;
        text-align: center;
        // padding: 0 1.4rem;
        button, a{
          display: inline-block;
          padding: 2rem 8rem;
          @include style_font(700, 1.8rem, 1, $font_color-AFAFAF_grey);
        }
        &.--active{
          left: 10%;
          height: 60px;
          background: linear-gradient(270deg, #44BCFF 0%, #4BA5FF 100%);
          border-radius: 1rem;
          button, a{
            color: white;
          }
        }
      } 
    }
    .tab-focus{
      width: 33.33%;
      transform: translateX(0);
    }
  }
  
  // * 🎈img 
  .img{
    display: block;
    background-repeat: no-repeat;
    background-size: cover;
  }
  
  // 고객문의 
  .img-customer-banner{
    background-image: url('../../public/asset/images/img_help_banner.png');
  }

  .img-no-result{
    background-image: url('../../public/asset/images/img_no_result.png');
  }
}

// TEMP page 
div.helloTmax{
  width: 70vw;
  margin: 0 auto;
  text-align: center;
  h1{
    padding: 5rem 0;
    font-size: 3rem;
  }
  h2{
    padding: 2rem 0;
    line-height: 1.4em;
    font-size: 2rem;
  }
  p{
    padding: 2rem 0;
    font-size: 1.4rem;
    line-height: 1.6em;
  }
  ul{
    padding-top: 4rem;
    padding-bottom: 10rem;
    li{
      padding-top: 1rem;
      display: flex;
      justify-content: space-between;
      font-size: 16px;
      .response{
        color: blueviolet;

      }
      a{
        color: blue;
        &:hover{
          text-decoration: underline;
        }
      }
    }
  }
  hr{
    margin: 0.4rem 0;
    background-color: darkblue;
  }
}


@include desktop-max{
  body{
    width: 100vw;
  }

  .App, .user{
    .w-default,
    .width1352{
      width: 100%;
    }
  
    position: relative;
    min-width: 100vw;

    overflow: hidden;
  }
}

@include mobile-max{
  body{
    font-size: 1.2rem;
  }
  
  .App, .user{
    .--pcview{
      display: none !important;
    }
    .--mobileview{
      display: initial;
    }
  
    .btn-more{ 
      .icon{
        margin-left: 6px;
        
        svg{
          width: 14px;
          height: 7px;
        }
      } 
    }
  }
}
