/* 404 page 전용
작업자: 정수진C
*/

section.notfound{
  width: 100vw;
  height: 100vh;

  display: flex;
  justify-content: center;
  align-items: center;
  div.container{
    text-align: center;
    img[alt="notfound"]{
      width: 30rem;
      height: 30rem;
    }
    h2{
      font-weight: 700;
      font-size: 2.4rem;
      line-height: 1.6em;
      padding-top: 4.4rem;
      padding-bottom: 3rem;
      color: #3C3C3C;
    }
    p{
      font-size: 1.4rem;
      line-height: 1.8em;
      color: #3C3C3C;
      padding-bottom: 3.2rem;
    }
    address{
      font-size: 1.4rem;
      letter-spacing: 0.2px;
      i.icon_call{
        width: 1.5rem;
        height: 1.5rem;

      }
      a{
        font-weight: 700;
        padding-left: 5px;
        padding-right: 5px;
        color: #2C99FF;
      }
      span{
        color: #B0B0B0;
      }
    }
  }

}